<style lang="sass" scoped>
	/*人员、部门、岗位选择器*/
	
	.chooser {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		background: rgba(255, 255, 255, 0.95);
		z-index: 9999;
		.search {
			padding: 10px;
			background: #F4F4F4;
			border-bottom: 1px solid #D9D9D9;
			input {
				border: 1px solid #EDEDEF;
				border-radius: 5px;
				box-shadow: none;
				height: 30px;
				width: 85%;
				padding-left: 2%
			}
		}
		.content {
			padding: 0 10px;
			overflow: auto;
			li {
				border-bottom: 1px solid #D9D9D9;
				padding: 14px 0;
			}
		}
	}
</style>
<template>
	<div v-show="show">
		<div class="chooser" :style="{'height':height+'px'}">
			<div class="search">
				<input type="text" name="" class="searchName" v-model="searchName" @keyup="getData" placeholder="请输入查询内容">
				<a href="javascript:void(0)" class="link" @click="cancel">取消</a>
			</div>
			<div class="content" :style="{'height':(height-53) +'px'}">
				<ul>
					<li v-for="item in dataList" @click="getSingle(item)" v-text="item.name"></li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script lang="babel">
	export default {
        props: {
            show: {
                type: Boolean,
                twoWay: true,
                default: false
            },
        	// type【user：用户，dept：部门，post：岗位】
        	chooserType:{
                type: String
        	}
		},
		data: function () {
			return {
				// 页面高度
				height: '',
				// 搜索名称
				searchName: '',
				// 查询的数据列表
				dataList: []
			}
		},
		ready: function () {
			this.getWindowGlobal();
		},
		methods: {
			// 设置弹出层高度
			getWindowGlobal: function () {
				this.height = window.screen.height;
			},
			// 获取列表数据
			getData: function () {
				let params = {
	                'name': this.searchName,
	                'type': this.chooserType
            	};
            	let typeName ='';
            	switch (this.chooserType) {
            		// 人员选择
            		case 'user':
            			typeName = 'staff';
            			break;
            		// 部门选择
            		case 'dept':
            			typeName = 'dept';
            			break;
            		// 岗位选择
            		case 'post':
            			typeName = 'post';
            			break;
            		// 人员选择
            		default:
            			typeName = 'staff';
            			break;
            	}
    			this.$http.get('static/data/'+ typeName +'.json', {'params': params}).then(function (res) {
    				res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                    this.dataList = res.data.data;
                });
			},
			// 获取选中行数据
			getSingle: function (obj) {
      			this.$emit('choosers', obj);
                this.show = false;
                this.clear();
			},
			// 取消
			cancel: function () {
                this.$emit('cancel');
                this.show = false;
                this.clear();
			},
			// 清空数据
			clear: function () {
				this.searchName = '';
				this.chooserType = '';
				this.dataList = '';
			}
		},
        watch: {
            show (value) {
                // 在显示时去掉body滚动条，防止出现双滚动条
                if (value) {
                    document.body.className += ' modal-open';
					$(".searchName").focus();
                }
                // 在modal动画结束后再加上body滚动条
                else {
                   document.body.className = document.body.className.replace(/\s?modal-open/, '');
                }
            }
        }
	}		
</script>


