<style lang="sass">
	div.buttonSeries {
		background-color: #fff;
		position: fixed;
		bottom: 0;
		width: 100%;
		z-index: 3;
		.buttonSeriesBox {
			padding: 8px 4px 8px 6px;
			height: auto;
		}
		.btn {
			border-color: #30a6e4;
			color: #30A6E4;
			height: 45px;
			width: 100%;
		}
		.save {
			width: 40%;
			margin-right: 2.5%;
			.btn {
				width:100%;
				color: #fff;
				background-color: #30A6E4;
			}
		}
		div.btnMore {
			border-radius: 5px;
			border: 1px;
		}
	}
	
	.btnGroupMore {
		width: 27%;
		margin-right: 2.5%;
		position: relative;
		.btnMore {
			position: absolute;
			left: 0;
			bottom: 40px;
			width: 100%;
		}
		.floatBtnGroup {
			position: absolute;
			top: 0;
			height: 100%;
			width: 100%;
			div {
				height: 30%;
				width: 100%;
				position: relative;
			}
			span {
				display: block;
				text-align: center;
				width: 100%;
				margin: -15% auto 0;
				position: absolute;
				top: 50%;
				font-size: 13px;
			}
			.abandon {
				margin-top: -12%;
			}
		}
	}
	
	.submit {
		width: 27%;
	}
	/*浮层*/
	
	.maskLayer {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
	}
	
	.reject,
	.abandonLayer,
	.replace {
		background: #efeff4;
	}
</style>
<template>
	<div class="buttonSeries">
		<div class="buttonSeriesBox clearfix">
			<div class="fl btnGroupMore">
				<button class="btn" :id="id" @click="btnMore">更多操作</button>
				<div class="btnMore" v-show="btnShow">
					<div>
						<img :src="floatButton" :alt="" :width="imgWidth" />
					</div>
					<div class="floatBtnGroup">
						<div><span class="abandon" @click="abandonShow">放弃</span></div>
						<div><span @click="rejectShow">驳回至</span></div>
						<div><span @click="replaceShow">更换处理人</span></div>
					</div>
				</div>
			</div>
			<div class="fl save"><button class="btn" @click="keepMsg">保存</button></div>
			<div class="fl submit"><button class="btn" @click="submitMsg">提交</button></div>
		</div>
	</div>
	<div class="maskLayer" @click="maskLayerShow" v-show="btnShow">
	</div>
	<!--驳回组件-->
	<reject :back-show-children.sync="childrenBackShow.showReject" v-if="childrenBackShow.showReject"></reject>
	<!--放弃-->
	<abandon :back-abandon.sync="backAbandonShow.showAbandon" v-if="backAbandonShow.showAbandon"></abandon>
	<!--更换处理人-->
	<!--<replace v-if="showReplace" class="replace"></replace>-->
	<!--div-->
	<replace :back-replace.sync="backReplaceShow.showReplace" v-if="backReplaceShow.showReplace"></replace>
</template>
<script lang="babel">
	import reject from './reject.vue'
	import abandon from './abandon.vue'
	import replace from	'./replace.vue'
	export default{
		name:'btnSeries',
		components:{reject,abandon,replace},
		data:function(){
			return {
				//按钮组
				btnShow:false,
				floatButton:'static/images/floatButton.png',
				imgWidth:'100%',
				childrenBackShow:{
					showReject:false
					
				},
				backAbandonShow:{
					showAbandon:false
				},
				//更换处理人
				backReplaceShow:{
					showReplace:false
				},
				submitData:''
			}
		},
		props: {
			keepMsgBtn: {
                type: Function,
                default: function () {
                }
          	 },
         	submitMsgBtn:{
           	 	type: Function,
                default: function () {
                }
          },
          id:{
                type:String,
                default:'modalCustomerID'
            }
  
		},
		
		methods:{
			//更多操作按钮
			btnMore:function(){
				this.btnShow=true;
				this.$broadcast('hrd-msg',this.id);
			},
			//遮罩层
			maskLayerShow:function(){
				this.btnShow=false;
			},
			//放弃是否显示
			abandonShow:function(){
				this.$emit('isclick',this.btnShow);
				this.btnShow=false;
				this.backAbandonShow.showAbandon=true;
			},
			//驳回是否显示
			rejectShow:function(){
				this.$emit('isclick',this.btnShow);
				this.btnShow=false;
				this.childrenBackShow.showReject=true;
			},
			//更换处理人
			replaceShow:function(){
				this.$emit('isclick',this.btnShow);
				this.btnShow=false;
				this.backReplaceShow.showReplace=true;
			},
			//保存信息
			keepMsg: function () {
                this.keepMsgBtn();
           },
           //提交信息
   			submitMsg:function(){
   				this.submitMsgBtn();
   			}
		},
		//监听信息变化
		 events: {
            'parent-msg': function(msg) {
                this.submitData=msg;
            }
        }
	}
</script>


