import app from './components/layout/app.vue';

import * as windowGlobal from './components/common/common.js';
window.windowGlobal = windowGlobal;

// 发起面试流程页面
import initiateInterview from './components/Home/initiateInterview.vue';

// 专业初试、复试、通道分会
import professionalTest from './components/Home/professionalTest.vue';

//hrdManger模块
import hrdManagerAuthorize from './components/Home/hrdManagerAuthorize.vue';

//GM审批模块
import hrdGmAuthorize from './components/Home/hrdGmAuthorize.vue';

//hrd审批
import hrdAuthorize from './components/Home/hrdAuthorize.vue';

Vue.config.debug = debug;
// var router = new Router({ linkActiveClass: 'selected', history: true});
// var router = new Router({ linkActiveClass: 'selected', history: true});
var router = new Router({ linkActiveClass: 'selected'});

router.map({
    "/home/initiateInterview": {
        name: 'initiateInterview',
        component: initiateInterview
    },
    "/home/professionalTest": {
        name: 'professionalTest',
        component: professionalTest
    },   
    "/home/hrdGmAuthorize": {
        name: 'hrdGmAuthorize',
        component: hrdGmAuthorize
    },
    "/home/hrdManagerAuthorize": {
        name: 'hrdManagerAuthorize',
        component: hrdManagerAuthorize
    },
     "/home/hrdAuthorize": {
        name: 'hrdAuthorize',
        component: hrdAuthorize
    },
    "*":{
    	name: 'initiateInterview',
    	component: initiateInterview
    }
});

router.redirect({
    '*': '/home/initiateInterview',
});
/* 实例化vue */
router.start({
    components: { app }
}, 'body');



