// 公共选择器
export function myBlur(id, index, target, data) {
    // 选择器值
    $("#" + id + index).html(JSON.stringify(data));
    // 隐藏域值
    $("#H" + id + index).html(JSON.stringify(data));
};

// 公共选择器
export function interviewDeptBlur(id, index, target, data) {
    //$("#" + id + index).html(JSON.stringify(data));
    $("#hdnInterviewDeptId").val(data[0].ID);
};

/*---------------------------------------------启动、禁用页面滚动条---------------------------------------------*/
var keys = { 32: 1, 37: 1, 38: 1, 39: 1, 40: 1 };
export function preventDefault(e) {
    e = e || window.event;
    e.preventDefault && e.preventDefault();
    e.returnValue = false;
}
export function preventDefaultForScrollKeys(e) {
    if (keys[e.keyCode]) {
        preventDefault(e);
        return false;
    }
}
// 记录原来的事件函数，以便恢复
var oldonwheel, oldonmousewheel1, oldonmousewheel2, oldontouchmove, oldonkeydown;
var isDisabled;
export function disableScroll() {
    if (window.addEventListener) {
        window.addEventListener('DOMMouseScroll', preventDefault, false);
    }
    oldonwheel = window.onwheel;
    window.onwheel = preventDefault; // modern standard
    oldonmousewheel1 = window.onmousewheel;
    window.onmousewheel = preventDefault; // older browsers, IE
    oldonmousewheel2 = document.onmousewheel;
    document.onmousewheel = preventDefault; // older browsers, IE
    oldontouchmove = window.ontouchmove;
    window.ontouchmove = preventDefault; // mobile
    oldonkeydown = document.onkeydown;
    document.onkeydown = preventDefaultForScrollKeys;
    isDisabled = true;
};
export function enableScroll() {
    if (!isDisabled) {
        return;
    }
    if (window.removeEventListener) {
        window.removeEventListener('DOMMouseScroll', preventDefault, false);
    }
    window.onwheel = oldonwheel; // modern standard
    window.onmousewheel = oldonmousewheel1; // older browsers, IE
    document.onmousewheel = oldonmousewheel2; // older browsers, IE
    window.ontouchmove = oldontouchmove; // mobile
    document.onkeydown = oldonkeydown;
    isDisabled = false;
};


