/*---------------------------------------------滚动条滚到页面顶部---------------------------------------------*/
export function myScroll() {
    //前边是获取chrome等一般浏览器 如果获取不到就是ie了 就用ie的办法获取 
    var x = document.body.scrollTop;
    var timer = setInterval(function () {
        x = x - 500;
        if (x < 500) {
            x = 0;
            window.scrollTo(x, x);
            clearInterval(timer);
        }
        window.scrollTo(x, x);
    }, "20");
}


