			//点击提交发送ajax请求
			btnSubmit:function(){
				this.submitPoint=false;
				this.$http.get('api/setHrdExmine',{'params': this.submitData}).then(function(res){
					res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
					if(res.data.Result==true){
						this.submitSuccess=true;
						var that=this;
						setTimeout(function(){
							that.submitSuccess=false;
						},1000)
					}
				}).catch(function (res) {
						
						this.submitFail=true;
						var that=this;
                           setTimeout(function(){
							that.submitFail=false;
					},1000)
                });
                window.location.href = "http://www.qq.com";
			},
			//控制浮层
			controlLayer:function(){
				this.inputPoint=false;
			},
			//此处是网页刚加载获取列表信息调用的方法
			getMsg: function(response) {
				for(var i = 0; i < response.data.posterAndMsgValue.length; i++) {
					this.posterAndMsg[i].value=response.data.posterAndMsgValue[i].value
				}
				//hr薪资建议
				for(var i = 0; i < response.data.hrManferSalaryValue.length; i++) {
					this.hrManferSalaryItems[i].value=response.data.hrManferSalaryValue[i].value
				};
				//下拉列表币种选项
				for(var i = 0; i < response.data.selectItems.length; i++) {
					this.selectItems.push(response.data.selectItems[i])
				}
				this.srcValue = this.src.gt;
				//薪资结构类型
				for(var i = 0; i < response.data.selectSalaryItems.length; i++) {
					this.selectSalaryItems.push(response.data.selectSalaryItems[i])
				}
				this.selectSalarySrc.srcValue = this.selectSalarySrc.src.gt;
				//年度服务月奖
				for(var i = 0; i < response.data.selectMonthItems.length; i++) {
					this.selectMonthItems.push(response.data.selectMonthItems[i])
				}
				this.selectMonthSrc.srcValue = this.selectMonthSrc.src.gt;
				//试用期
				this.selectUserSrc.srcValue = this.selectUserSrc.src.gt;
				//其他薪资信息展开or合起来
				this.otherSalaryMsgSrc.srcValue=this.otherSalaryMsgSrc.src.gt;
				//安家费发放时间
				this.homeMoneyTimeSrc.srcValue=this.homeMoneyTimeSrc.src.gt;
				//签字费发放时间
				this.signMoneyTimeSrc.srcValue=this.signMoneyTimeSrc.src.gt;
			},
			selectMoney: function() {
				if(this.gtImgStatus) {
					this.srcValue = this.src.lt;
					this.gtWidth = 15;
					this.gtImgStatus = false;
				} else {
					this.srcValue = this.src.gt;
					this.gtWidth = 10;
					this.gtImgStatus = true;
				}
			},
			salaryType: function() {
				if(this.selectSalarySrc.gtImgStatus) {
					this.selectSalarySrc.srcValue = this.selectSalarySrc.src.lt;
					this.selectSalarySrc.gtWidth = 15;
					this.selectSalarySrc.gtImgStatus = false;
				} else {
					this.selectSalarySrc.srcValue = this.selectSalarySrc.src.gt;
					this.selectSalarySrc.gtWidth = 10;
					this.selectSalarySrc.gtImgStatus = true;
				}
			},
			yearReward: function() {
				if(this.selectMonthSrc.gtImgStatus) {
					this.selectMonthSrc.srcValue = this.selectMonthSrc.src.lt;
					this.selectMonthSrc.gtWidth = 15;
					this.selectMonthSrc.gtImgStatus = false;
				} else {
					this.selectMonthSrc.srcValue = this.selectMonthSrc.src.gt;
					this.selectMonthSrc.gtWidth = 10;
					this.selectMonthSrc.gtImgStatus = true;
				}
			},
			userTime: function() {
				if(this.selectUserSrc.gtImgStatus) {
					this.selectUserSrc.srcValue = this.selectUserSrc.src.lt;
					this.selectUserSrc.gtWidth = 15;
					this.selectUserSrc.gtImgStatus = false;
				} else {
					this.selectUserSrc.srcValue = this.selectUserSrc.src.gt;
					this.selectUserSrc.gtWidth = 10;
					this.selectUserSrc.gtImgStatus = true;
				}
			},
			//展示其他薪资信息
			otherSalaryMsgShow:function(){
				if(this.otherSalaryMsgSrc.gtImgStatus==true){
					this.otherSalaryMsg=true;
					this.otherSalaryMsgSrc.srcValue = this.otherSalaryMsgSrc.src.lt;
					this.otherSalaryMsgSrc.gtWidth = 15;
					this.otherSalaryMsgSrc.gtImgStatus=false;
				}else{
					this.otherSalaryMsg=false;
					this.otherSalaryMsgSrc.srcValue = this.otherSalaryMsgSrc.src.gt;
					this.otherSalaryMsgSrc.gtWidth = 10;
					this.otherSalaryMsgSrc.gtImgStatus=true;
				}
				
			},
			//安家费发放时间
			homeMoneyTimeShow:function(){
				if(this.homeMoneyTimeSrc.gtImgStatus==true){
					this.homeMoneyTimeSrc.srcValue = this.homeMoneyTimeSrc.src.lt;
					this.homeMoneyTimeSrc.gtWidth = 15;
					this.homeMoneyTimeSrc.gtImgStatus=false;
				}else{
					this.homeMoneyTimeSrc.srcValue = this.homeMoneyTimeSrc.src.gt;
					this.homeMoneyTimeSrc.gtWidth = 10;
					this.homeMoneyTimeSrc.gtImgStatus=true;
				}
				
			},
			//签字费发放时间
			signMoneyTimeShow:function(){
				if(this.signMoneyTimeSrc.gtImgStatus==true){
					this.signMoneyTimeSrc.srcValue = this.signMoneyTimeSrc.src.lt;
					this.signMoneyTimeSrc.gtWidth = 15;
					this.signMoneyTimeSrc.gtImgStatus=false;
				}else{
					this.signMoneyTimeSrc.srcValue = this.signMoneyTimeSrc.src.gt;
					this.signMoneyTimeSrc.gtWidth = 10;
					this.signMoneyTimeSrc.gtImgStatus=true;
				}
				
			},
			// 获取语音信息
			getAchievement: function (msg) {
				this.submitData.inputVal = msg;
			},
			// 滚动至顶部
			getBtnClick: function (msg) {
				this.isBtnClick = true;
				windowGlobal.myScroll();
			}
		},
		
		ready: function() {
			//此处需要从json处取数据如果有薪资，则shou=false
			this.$http.get('static/data/employeeData.json').then((response) => {
				response.data = $.parseJSON(response.data) == null? response.data: $.parseJSON(response.data);
				this.getMsg(response);
				//薪资证明的变量
				var	prove=response.data.salaryProve
				if(prove){
					this.show=false;	
				}else{
					this.show=true
				}
			}, (response) => {});
			
		},
		events: {
			'advise-msg': function(msg) {
				this.advise=msg
			}
		},
		watch: {
            'advise': {
                handler: function (val,oldval) {
                	if(val!=oldval){
                		this.submitData.inputVal=val;
                	}
                	
                },
                deep: true
            }
        }
	}
</script>


