		methods: {
			// 获取简历列表
			getResumes: function (moreNum) {		
				this.isResume = false;
                let params = {
	                'name': this.resumeName,
	                'num': this.loadMoreNum === 2 ? this.loadMoreNum : moreNum
            	};
                this.loadings=true;
                this.$http.get('static/data/resume.json', {'params': params}).then(function (res) {
                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                	this.loadings=false;
                    this.resumeList = res.data.data;
                });
			},
			// 设置当前应聘人
			setResume: function (obj) {
				this.resume.id = obj.id;
				this.resume.name = obj.name;
				this.isResume = true;
			},
			// 打开选择器弹窗
			setOpenChooser: function (typeName, id) {
				this.chooserType = typeName;
				this.isOpenChooser =true;
				this.currentUserId = id;
			},
			// 获取当前选择器选择的对象
			getChooserId: function (msg) {
				if (this.chooserType === 'dept') {
					this.getCurrentDept(msg);
				} else if (this.chooserType === 'post') {
					this.getCurrentPost(msg);
				} else if (this.chooserType === 'user') {
					this.getCurrentUser(msg);
				}
			},
			// 当前部门
			getCurrentDept: function (msg) {
				this.isOpenChooser = false;	
				this.currentDept = msg;
				// 获取当前部门对应的具体数据
                let params = {
	                'id': this.currentDept.id
            	};
                this.$http.get('static/data/dept.json', {'params': params}).then(function (res) {
                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                    this.currentDeptDetail = res.data.data[0];
                });
			},
			// 当前岗位
			getCurrentPost: function (msg) {
				this.isOpenChooser = false;				
				this.currentPost = msg;
				// 获取当前部门对应的具体数据
                let params = {
	                'id': this.currentPost.id
            	};
                this.$http.get('static/data/post.json', {'params': params}).then(function (res) {
                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                    this.currentPostDetail = res.data.data[0];
                });
			},
			// 新增通道分会复试、面委会--修改
			addProcess: function (id, parents) {
				this.countLength ++;
				let newParents = -1;
				if (id === 5 || id === 6) {
					newParents = id;
				} else {
					newParents = parents;
				}
				let newInfo = {					
                    "id": this.countLength,
                    "type": 'test' + this.countLength,
                    "name": newParents === 5 ? '通道分会复试':'面委会复试',
                    "operatUser": "",
                    "operatUserId": '',
                    "parents": newParents,
                    "isShowSwitch": true,
                    "switchStatus": true,
                    "isShow":true,
                    "isNewCreate":true,
                    "isNewLast":true
				}
				for (var i = 0; i < this.interviewProcess.length; i++) {
					if (this.interviewProcess[i].isNewLast) {
						if (this.interviewProcess[i].id === id ) {
							this.interviewProcess[i].isNewLast = false;
							this.interviewProcess.splice(i + 1, 0, newInfo);
						}
					}
				}
			},
			// 当前操作人
			getCurrentUser: function (msg) {
				for (var i = 0; i < this.interviewProcess.length; i++) {
					if (this.interviewProcess[i].id === this.currentUserId) {
						this.interviewProcess[i].operatUserId = msg.id;
						this.interviewProcess[i].operatUser = msg.name;
					}
				}
			},
			// 设置面试环节-删除新增行后设置其他状态
			getLastRow: function (id, parents) {
				for (var n = 0; n < this.interviewProcess.length; n++) {
					if (this.interviewProcess[n].id !== id && this.interviewProcess[n].parents === parents) {
						this.newRowTemp.push(this.interviewProcess[n]);
					}
				}
				if (this.newRowTemp.length > 0) {
					this.newRowTemp[this.newRowTemp.length - 1].isNewLast = true;
				} else {
					this.interviewProcess[parents - 1].isNewLast = true;
				}
			},
			// 设置面试环节开关
			setToggle: function (bool, id) {
				this.newRowTemp = [];
				for (var i = 0; i < this.interviewProcess.length; i++) {
					if (this.interviewProcess[i].id === id) {
						if (!bool && this.interviewProcess[i].isNewCreate) {
							this.tipsBox.tipBoxClose = true;
		                    this.tipsBox.tipContent = "是否要删除"+this.interviewProcess[i].name+"?";
		                    this.tipsBox.tipButton = 3;
		                    var temp = i;
		                    var parents = this.interviewProcess[i].parents;
			                this.tipsBox.tipConfirmBtn = function () {
								this.getLastRow(id, parents);
								this.interviewProcess.splice(temp, 1);
			                }.bind(this);
		                    this.tipsBox.isShow = true;
						} else {
							this.interviewProcess[i].switchStatus = bool;
							this.interviewProcess[i + 1].isShow = bool;
							if (!bool && id === 2) {
								this.interviewProcess[2].switchStatus = bool;
								this.interviewProcess[2].isShow = bool;
								this.interviewProcess[3].switchStatus = bool;
								this.interviewProcess[3].isShow = bool;
							}
						}
					}
				}
				this.interviewProcess[4].isShow = true;
				this.interviewProcess[5].isShow = true;
				this.interviewProcess[6].isShow = true;
				this.interviewProcess[7].isShow = true;
				this.interviewProcess[8].isShow = true;
			},
			// 面试安排：是否一键安排面试
			setGhostIntreview: function (bool) {
				this.interviewScheduled.isOnlyInter = bool;
			},
			// 上一步
			lastStep: function (type) {
				// 面试流程中的"上一步"
				if (type === 1) {
					this.isProcess = false;
					this.interviewScheduled.isOnlyInter = false;
				}
				// 面试安排中的"上一步"
				if (type === 2) {
					this.isInterviewScheduled = false;
				}
			},
            // 获取会议室信息
            getInterviewScheduled: function () {
                this.loadings = true;
                this.$http.get('static/data/interviewScheduled.json').then(function (res) {
                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                	this.loadings = false;
                    if (res.data.Result === true) {
                        this.interviewBoardroomList = res.data;
                    } else {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "请检查网络！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    }
                }).catch(function (res) {
                	this.loadings = false;
                });
            },
			// 下一步
			nextStep: function (type) {
				// 进入“面试流程”页面
				if (type === 1) {
					if (this.resume.id && this.currentDept.id && this.currentPost.id) {
						this.isProcess = true;
					}	
				}
				// 在“面试流程”页面点击了“下一步”按钮
				if (type === 2) {
					this.isProcessStatus = true;
					var flag = true;
					for (var i = 0; i < this.interviewProcess.length; i++) {
						if ((this.interviewProcess[i].isRequired && this.interviewProcess[i].operatUser === '') || (this.interviewProcess[i].switchStatus && this.interviewProcess[i].operatUser === '')) {
							this.tipsBox.tipBoxClose = true;
		                    this.tipsBox.tipContent = "请将信息填写完整！";
		                    this.tipsBox.tipButton = 2;
		                    this.tipsBox.isShow = true;
		                    flag = false;
						}
					}
					// 显示面试安排
					if (flag) {
						this.isInterviewScheduled = true;
						this.allInterviewUsers = [];
						for (var i = 0; i < this.interviewProcess.length; i++) {
							if (this.interviewProcess[i].operatUser) {
								var obj = {};
									obj.operatUser = this.interviewProcess[i].operatUser;
									obj.operatUserId = this.interviewProcess[i].operatUserId;
								this.allInterviewUsers.push(obj);
							}
						}
						this.setInterview(this.allInterviewUsers);
						// 设置默认的面试类型
						if (this.interviewerUsers.length === 1) {
							this.interviewScheduled.interType = 1;
						} else if (this.interviewerUsers.length >= 2) {
							this.interviewScheduled.interType = 2;
						}
						if (this.allInterviewUsers.length > 0 ) {
							this.interviewScheduled.isOnlyInter = true;
						}
					}
				}
			},
			// 面试安排
			setInterview: function (obj) {
				this.idArrs = [];
				this.objArrs = [];
				this.interviewerUsersId = [];
				this.getInterviewScheduled();				
                for (var i = 0; i < obj.length; i++) {
                    if (this.idArrs.indexOf(obj[i].operatUserId) < 0) {
                        this.idArrs.push(obj[i].operatUserId);
                        this.objArrs.push(obj[i]);
                    }
                }
                this.interviewerUsers = this.objArrs;

                // 拼接字符串
                var userStr = '';
                for (var i = 0; i <this.objArrs.length; i++) {
                	let wrap = "";
                    userStr += this.objArrs[i].operatUser + ';<br>';
                    this.interviewerUsersId.push(this.objArrs[i].operatUserId);
                }
				// 设置面试安排值
				this.interviewScheduled.interUser = userStr;
			},
			// 设置面试官名单
			setInterviewList: function () {
				this.isInterviewList = true;
			},
			// 获取面试官名单组件中传的值
			getInterivewsReturn: function (msg) {
				this.setInterview(msg);
			},
			// 设置面试类型
			setInterviewType: function (type) {
				this.interviewScheduled.interType = type;
				this.checkInterview();
			},
			// 面试安排中面试类型和面试官的校验
			checkInterview: function () {
				if (this.interviewScheduled.interType === 1 && this.interviewerUsers.length > 1) {
					// 单面
					this.tipsBox.tipBoxClose = true;
                    this.tipsBox.tipContent = "当面试类型为'单面'时，面试官只能为一个！";
                    this.tipsBox.tipButton = 2;
                    this.tipsBox.isShow = true;
                    return false;
				} else if ((this.interviewScheduled.interType === 2 || this.interviewScheduled.interType === 3) && this.interviewerUsers.length < 2) {
					// 群面
					this.tipsBox.tipBoxClose = true;
                    this.tipsBox.tipContent = "当面试类型为'面试官多对1'或者'多轮1对1'时，面试官必须为两个以上！";
                    this.tipsBox.tipButton = 2;
                    this.tipsBox.isShow = true;
                    return false;
				} else {
					return true;
				}
			},
			// 不为空判断
			validateForm: function () {
				let day = new Date();
				let today = day.getFullYear()+"-"+(day.getMonth()+1)+"-"+day.getDate();
				let tip = '';
				if (!this.interviewScheduled.interMode) {
					tip = "请选择'期望面试方式'!";
				} else if (!this.interviewScheduled.interTime) {
					tip = "请选择'期望面试时间'!";
				} else if (this.interviewScheduled.interTime < today) {
					tip = "请选择今天之后的时间!";
				} else if (!this.interviewScheduled.interAddress) {
					tip = "请选择'期望面试地点'!";
				// } else if (!this.interviewScheduled.interFloor) {
				// 	tip = "请选择'期望面试楼层'!";
				} else if (!this.interviewScheduled.interUser) {
					tip = "请填写'面试官'!";
				}
				return tip;				
			},
			// 发起面试流程校验
			startIntreviewProcess: function () {
				if (this.interviewScheduled.isOnlyInter) {
					this.checkInterview();
					let tip = this.validateForm();
					if(tip !== '') {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = tip;
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;					
					} else if (this.checkInterview()) {
						this.isInterviewValidate = true;
					}
				} else {
	                let params = {
		                'interUser': this.interviewScheduled.interUser
	            	};
	                this.loadings = true;
	                this.$http.post('api/setInterview', {'params': params}).then(function (res) {
	                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
	                	this.loadings = false;
	                    if (res.data.Result === true) {
							this.tipsBox.tipBoxClose = true;
		                    this.tipsBox.tipContent = "发起面试成功！";
		                    this.tipsBox.tipButton = 1;
		                    this.tipsBox.isShow = true;
	                    } else {
							this.tipsBox.tipBoxClose = true;
		                    this.tipsBox.tipContent = "请检查网络！";
		                    this.tipsBox.tipButton = 2;
		                    this.tipsBox.isShow = true;
	                    }
	                }).catch(function (res) {
	                	this.loadings = false;
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "请检查网络！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
	                });

				}
			},
			// 发起面试流程
			setIntreviewProcess: function () {
                let params = {
	                'interMode': this.interviewScheduled.interMode,
	                'interTime': this.interviewScheduled.interTime,
	                'interAddress': this.interviewScheduled.interAddress,
	                // 'interFloor': this.interviewScheduled.interFloor,
	                'interUser': this.interviewScheduled.interUser,
	                'makeInter': this.interviewScheduled.makeInter
            	};
                this.loadings = true;
                this.$http.post('api/setInterview', {'params': params}).then(function (res) {
                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                	this.loadings = false;
                    if (res.data.Result === true) {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "发起面试成功！";
	                    this.tipsBox.tipButton = 1;
	                    this.tipsBox.isShow = true;
                    } else {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "请检查网络！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    }
                }).catch(function (res) {
                	this.loadings = false;
					this.tipsBox.tipBoxClose = true;
                    this.tipsBox.tipContent = "请检查网络！";
                    this.tipsBox.tipButton = 2;
                    this.tipsBox.isShow = true;
                });
                window.location.href = "http://www.qq.com";
			}
		}
	}
</script>


