						type: 'radar',
						data: [{
							value: averageHistory,
							name: '历史平均值'
						}, {
							value: averageScore,
							name: '平均得分'
						}],
						symbolSize: 12,
						symbol: "circle",
						animation: false
					}]
				});

			},
			//试用期
			userTime: function() {
				if(this.selectUserSrc.gtImgStatus) {
					this.selectUserSrc.srcValue = this.selectUserSrc.src.lt;
					this.selectUserSrc.gtWidth = 15;
					this.selectUserSrc.gtImgStatus = false;
				} else {
					this.selectUserSrc.srcValue = this.selectUserSrc.src.gt;
					this.selectUserSrc.gtWidth = 10;
					this.selectUserSrc.gtImgStatus = true;
				}
			},
			//综合评价
			evaluate: function() {
				if(this.selectEvaluateSrc.gtImgStatus) {
					this.selectEvaluateSrc.srcValue = this.selectEvaluateSrc.src.lt;
					this.selectEvaluateSrc.gtWidth = 15;
					this.selectEvaluateSrc.gtImgStatus = false;
				} else {
					this.selectEvaluateSrc.srcValue = this.selectEvaluateSrc.src.gt;
					this.selectEvaluateSrc.gtWidth = 10;
					this.selectEvaluateSrc.gtImgStatus = true;
				}
			},
			//点击说明
			instruction: function() {
				this.scoreInstructionShow = true;
			},
			//关闭说明
			closeInstruction: function() {
				this.scoreInstructionShow = false;
			},
			// 获取语音信息
			getAchievement: function (msg) {
				this.submitData.inputVal = msg;
			},
			// 滚动至顶部
			getBtnClick: function (msg) {
				this.isBtnClick = true;
				windowGlobal.myScroll();
			}
		},
		ready: function() {
			this.$http.get('static/data/employeeData.json').then((response) => {
				response.data = $.parseJSON(response.data) == null? response.data: $.parseJSON(response.data);
				this.getMsg(response);
			}, (response) => {});
		},
		events: {
			'advise-msg': function(msg) {
				this.advise = msg
			}
		},
		watch: {
			'advise': {
				handler: function(val, oldval) {
					if(val != oldval) {
						this.submitData.inputVal = val;
					}
				},
				deep: true
			}
		}
	}
</script>


