<style scoped lang="sass">
	div#hrdApprove {
		div.hrdApprove,
		div.hrdApproveCon,
		div.hrdApprovePoster {
			border-bottom: none;
			background-color: #fff;
		}
		div.hrdApprove {
			height: 40px;
			border-top: 1px solid rgba(204, 204, 204, 1);
			.hrdApproveTitle {
				margin: 10px 0 0 24px;
				h4 {
					font-weight: normal;
				}
			}
		}
		div.hrdApproveCon,
		div.hrdApproveResign,
		div.hrdApprovePoster,
		div.hrdApproveSeeSalary {
			width: 95%;
			margin: 10px auto;
			border: none;
			border-radius: 5px;
			color: #868686;
		}
		ul {
			padding: 5px 0 5px 15px;
			li:first-child {
				margin-top: 10px;
			}
			li+li {
				margin-top: 15px;
			}
			li:last-child {
				margin-bottom: 15px;
			}
			span {
				line-height: 20px;
				display: inline-block;
			}
			span+span {
				color: #1e1e1e;
				margin-left: 20px;
			}
		}
		div.hrdApprovePoster {
			p {
				padding: 10px 0 10px 13px;
			}
			.workMsg {
				margin-bottom: 0;
			}
		}
		div.hrdApproveSalary {
			ul:last-of-type {
				li {
					span:first-child {
						width: 38%;
					}
				}
			}
		}
		.salary {
			margin: 0 5px;
			span {
				padding: 2px 4px;
				display: block;
			}
		}
	}
	/*审批意见*/
	
	.approveAdvise {
		border-top: 1px solid #d3d3d3;
		>div {
			padding: 15px 20px 0 15px;
			>div {
				height: 100%;
				box-sizing: border-box;
			}
		}
	}
</style>

<template>
	<div class=""> 
		<!-- 导航开始 -->
		<right-head>
			<li>
				<a>招聘选拔 </a>
				<span>&gt;</span>
				<a>社会招聘</a>
				<span>&gt;</span>
				<a>面试流程跟踪</a>
				<span>&gt;</span>
				<a>hrd审批</a>
			</li>
		</right-head>
		<!-- 导航结束 -->

		<div class="hrd-authorize">
			<interview-public>
				<div id="hrdApprove">
					<!--hrd审批-->
					<div class="hrdApprove">
						<div class="hrdApproveTitle">
							<h4>HRD审批</h4>
						</div>
					</div>
					<!--员工类型-->
					<div class="hrdApproveCon">
						<ul>
							<li v-for="item in conItems"><span>{{item.name}} </span><span> {{item.value}}</span></li>
						</ul>
					</div>
					<!--离职回流-->
					<div class="hrdApproveResign">
						<ul>
							<li><span>{{resign.resignText}}</span><span>{{resign.resignDetermine}}</span></li>
						</ul>
					</div>
					<!--安排岗位信息-->
					<div class="hrdApprovePoster">
						<ul>
							<li class="workMsg"><span>安排岗位信息</span></li>
						</ul>
						<ul>
							<li v-for="item in posterItems">
								<span>{{item.name}}</span><span>{{item.value}}</span>
							</li>
						</ul>
					</div>
					<div class="hrdApprovePoster hrdApproveSalary">
						<ul>
							<li class="workMsg"><span>HR建议薪酬信息</span></li>
						</ul>
						<ul>
							<li v-for="item in salaryItems">
								<span>{{item.name}}</span><span>{{item.value}}</span>
							</li>
						</ul>

					</div>
					<!--查看薪资-->
					<div class="hrdApproveSeeSalary">
						<div v-show="show" class="salary"><span>该offer薪酬建议值已超出目标线，由于"脱产学习一年以上"不能提供薪资证明。</span></div>
						<div v-else class="salary">
							<span> 如需查看薪资证明，请到web端。 </span>
						</div>
					</div>
				</div>
				<!--审批意见-->
				<div class="approveAdvise">
					<div class="">
						<h4><img src="../../../static/images/star.png" width="10" /> 审批意见
						</h4>
					</div>
					<approve-advise :advise-info="advise" @approve="getAchievement"></approve-advise>
				</div>


				<!--填写不完整的提示信息-->
				<div class="inputPoint" v-show="inputPoint" @click="controlLayer">
					<div class="inputPointMsg">
						请将必填项填写完整
					</div>
				</div>
				<!--提交成功的提示-->
				<div class="submitPoint" v-show="submitPoint">
					<div class="submitMsg">
						<h2 style="">确认要提交信息</h2>
						<div class="clearfix">
							<span class="fl" @click="submitPoint=false">取消</span>
							<span class="submitBtn fl" @click="btnSubmit">提交</span>
						</div>

					</div>
				</div>
				<!--保存成功-->
				<div class="savePoint" v-show="saveShow">
					<div class="saveMsg submitMsg">
						<h2 style="">成功保存</h2>
					</div>
				</div>
				<!--提交成功-->
				<div class="submitPoint" v-show="submitSuccess">
					<div class="submitMsg">
						<h2 style="">提交成功</h2>
					</div>
				</div>
				<!--提交失败-->
				<div class="submitPoint" v-show="submitFail">
					<div class="submitMsg">
						<h2 style="">提交失败，请检查网络重试</h2>
					</div>
				</div>
				<!--按钮系列-->
				<btn-series :keep-msg-btn="btnSeries.keepMsgBtn" :submit-msg-btn="btnSeries.submitMsgBtn" id="hrd"  v-on:isclick="getBtnClick"></btn-series>
			</interview-public>
		</div> 
		<!-- <button style="margin-bottom:150px; " @click="bodyTest">点击回到顶部</button> -->
	</div>
</template>
<script lang="babel">
	import btnSeries from '../Parts/btnSeries.vue'
	import approveAdvise from '../Parts/approveAdvise.vue'
	import rightHead from '../common/rightHead.vue'
	import interviewPublic from '../Parts/interviewPublic.vue'
	export default{
		components:{
			btnSeries,
			approveAdvise,
			rightHead,
			interviewPublic
		},
		data: function () {
			return {
				btnSeries: {
                   	keepMsgBtn:this.keepMessages,
                    submitMsgBtn:this.submitMessages
                },
				advise: '',
				//保存按钮
				saveShow:false,
				//提交表单不完整的提示
				inputPoint:false,
				//表单提交
				submitPoint:false,
				//表单成功提交
				submitSuccess:false,
				//提交失败
				submitFail:false,
				//员工类型的列表
				conItems:[
					{
						"name":"员工类型",
						"value":''
					},
					{
						"name":"需求职级",
						"value":''
					},
					{
						"name":"学历要求",
						"value":''
					},
					{
						"name":"工作年限",
						"value":''
					},
					{
						"name":"需求类型",
						"value":''
					}
					
				],
				resign:{
					resignText:'离职回流',
					resignDetermine:''
				},
				//安排岗位信息列表
				posterItems:[
					{
						"name":"安排职位",
						"value":''
					},
					{
						"name":"建议职级",
						"value":''
					},{
						"name":"建议子等",
						"value":''
					},{
						"name":"安排组织",
						"value":''
					},{
						"name":"安排岗位",
						"value":''
					},{
						"name":"特殊岗位",
						"value":''
					},{
						"name":"降级录用",
						"value":''
					},{
						"name":"学历特批",
						"value":''
					},{
						"name":"背景调查",
						"value":''
					}
				],
				//薪资信息列表
				salaryItems:[
					{
						"name":"期望薪酬",
						"value":''
					},
					{
						"name":"期望薪资说明",
						"value":''
					},{
						"name":"建议薪酬",
						"value":''
					},{
						"name":"币种",
						"value":''
					},{
						"name":"薪资结构类别",
						"value":''
					},{
						"name":"浮动比例",
						"value":''
					},{
						"name":"年度服务奖励月数",
						"value":''
					},{
						"name":"含长期激励",
						"value":''
					}
				],
				//控制薪资证明是否显示，此处需要从js处获取
				show:true,
				//提交传输的数据
				submitData:{
					//审批意见的值
					inputVal:''
				},
				// 是否点击了更多操作
				isBtnClick: false
			}
		},
		methods:{
			//点击保存按钮，保存信息
			keepMessages: function () {
				// if(this.submitData.inputVal==''){
				// 	//信息不完整提示出来
				// 	this.inputPoint=true;
				// }else{
					this.saveShow=true;
					var that=this;
					setTimeout(function(){
						that.saveShow=false;
					},2000)
				// }
			},
			//点击提交按钮，提交信息
			submitMessages:function(){
				if(this.submitData.inputVal==''){
					this.inputPoint=true;
				}else{
					this.submitPoint=true;
				}
			},
			//点击提交发送ajax请求
			btnSubmit:function(){
				this.submitPoint=false;
				this.$http.get('api/setHrdExmine',{'params': this.submitData}).then(function(res){
					res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
					if(res.data.Result==true){
						this.submitSuccess=true;
						var that=this;
						setTimeout(function(){
							that.submitSuccess=false;
						},1000)
					}
				}).catch(function (res) {
						this.submitFail=true;
						var that=this;
                           setTimeout(function(){
							that.submitFail=false;
					},1000)
                });
                window.location.href = "http://www.qq.com";
			},
			//控制浮层
			controlLayer:function(){
				this.inputPoint=false;
			},
			// 获取语音信息
			getAchievement: function (msg) {
				this.submitData.inputVal = msg;
			},
			// 滚动至顶部
			getBtnClick: function (msg) {
				this.isBtnClick = true;
				windowGlobal.myScroll();
			}
		},		
		ready:function(){
			//此处需要从json处取数据如果有薪资，则shou=false
			this.$http.get('static/data/employeeData.json').then((response) => {
				response.data = $.parseJSON(response.data) == null? response.data: $.parseJSON(response.data);
				for(var i=0;i<response.data.employeeDataValue.length;i++){
					this.conItems[i].value=response.data.employeeDataValue[i].value
				}
				//离职回流
				this.resign.resignDetermine=response.data.resignValue.resignDetermine;
				//安排岗位信息列表
				for(var i=0;i<response.data.posterItemsValue.length;i++){
					this.posterItems[i].value=response.data.posterItemsValue[i].value
				}
				//薪资信息列表
				for(var i=0;i<response.data.salaryItemsValue.length;i++){
					this.salaryItems[i].value=response.data.salaryItemsValue[i].value
				}
					//response.data.salaryProve  薪资证明的变量
					var	prove=response.data.salaryProve
					if(!prove){
						this.show=false;	
					}else{
						this.show=true
					}
				}, (response) => {
			});
			
		},
		events: {
			'advise-msg': function(msg) {
				this.advise=msg;
			} 
		},
		watch: {
            'advise': {
                handler: function (val,oldval) {
                	if(val!=oldval){
                		this.submitData.inputVal=val;
                	}
                },
                deep: true
            }
        }
	}
	
</script>


