<style scoped lang="sass">
	h4 {
		font-weight: normal;
	}
	
	div.interviewMsg {
		margin-top: 15px;
		.interviewTitle {
			padding-left: 24px;
			height: 40px;
			border-bottom: 1px solid rgba(204, 204, 204, 1);
			background: #fff;
			line-height: 40px;
			border-top: 1px solid rgba(204, 204, 204, 1);
			img {
				right: 20px;
				top: 12px;
			}
		}
		.interviewMain {
			margin: 10px auto;
			border-radius: 5px;
			background: #fff;
			width: 95%;
			span {
				display: inline-block;
			}
			span:nth-of-type(1) {
				margin-right: 0px;
				width: 30%;
				color: #868686;
			}
			>div {
				padding: 10px 10px 10px 15px;
			}
			.personalMsg {
				margin-bottom: 15px;
				span:nth-of-type(1) {
					font-size: 18px;
					color: rgb(51, 51, 51);
				}
			}
			a {
				text-decoration: none;
			}
			li {
				margin-bottom: 15px;
				.telImg {
					top: 0;
					left: 65%;
					background: #efeff4;
					width: 48px;
					height: 23px;
					border: 1px solid rgba(204, 204, 204, 1);
					border-radius: 5px;
					img {
						vertical-align: middle;
						margin-left: 2px;
					}
				}
			}
		}
		.interviewArrange {
			span {
				display: block;
				float: left;
				width: 70%;
			}
		}
		span:nth-of-type(1) {
			width: 30%;
		}
		#interviewSituation {
			li {
				margin-bottom: 5px;
			}
			.interviewMainBox {
				background: url(../../../static/images/line.png) no-repeat 20px 12px;
			}
			.moreMsg {
				margin-left: -60px;
				background: #fff;
				margin-top: 10px;
				border-top: 1px solid rgba(204, 204, 204, 1);
				border-bottom: 1px solid rgba(204, 204, 204, 1);
				li {
					margin-bottom: 10px;
				}
				li:first-child {
					margin-top: 10px;
				}
			}
			.closeMore {
				display: inline-block;
				width: 90px;
				border: 1px solid rgba(204, 204, 204, 1);
				border-radius: 5px;
				line-height: 25px;
				height: 25px;
				margin-bottom: 10px;
				color: #30A6E4;
			}
		}
	}
	
	.loadMore {
		width: 90px;
		text-align: center;
		border: 1px solid rgba(204, 204, 204, 1);
		height: 25px;
		border-radius: 5px;
		color: #30A6E4;
		line-height: 25px;
		margin-top: 10px;
	}
	/*审批过程*/
	
	div.approveProcess {
		margin-top: 25px;
		padding-bottom: 20px;
		div.hrdApprove {
			height: 40px;
			border-top: 1px solid rgba(204, 204, 204, 1);
			border-bottom: 1px solid rgba(204, 204, 204, 1);
			background-color: #fff;
			margin-top: 20px;
			.hrdApproveTitle {
				margin: 10px 0 0 24px;
				img {
					right: 20px;
					top: 4px;
				}
			}
		}
		div.approveMain {
			margin: 10px auto;
			width: 95%;
			background: #fff;
			border-radius: 5px;
			color: #868686;
			.step {
				margin-left: 20px;
				width: 80%;
				padding-bottom: 30px;
				li {
					margin-bottom: 10px;
					span {
						display: block;
						width: 30%;
						float: left;
						line-height: 27px;
					}
					span:nth-of-type(2) {
						display: block;
						width: 70%;
						color: #1e1e1e;
					}
				}
			}
			/*内容盒子*/
			.approveMainBox {
				padding-top: 20px;
				margin: 0 10px;
			}
			.approveMainBox+.approveMainBox {
				border-top: 1px solid rgba(204, 204, 204, 1);
			}
		}
	}
</style>
<template>
	<div class="">
		<div id="">
			<div class="interviewMsg">
				<!--应聘人信息-->
				<div>
					<div class="interviewTitle pr" @click="arrowTitle=!arrowTitle">
						<h4>应聘人信息 - {{interviewPersonMsg.name}}</h4>
						<img src="../../../static/images/arrowDown.png" alt="" width="20" class="pa" v-show="arrowTitle" />
						<img src="../../../static/images/arrowUp.png" alt="" width="20" class="pa" v-else/>
					</div>
					<div class="interviewMain" v-show="arrowTitle">
						<div>
							<div class="personalMsg">
								<span>{{interviewPersonMsg.name}}</span><span>{{interviewPersonMsg.sex}}</span><a href="javascript:void(0)" class="fr link">查看简历</a>
							</div>
							<ul>
								<li class="pr"><span>手机号</span><span>{{interviewPersonMsg.tel}}</span>
									<span class="pa telImg"><img src="../../../static/images/tel.png" height="20"/></span>
								</li>
								<li><span>电子邮箱</span><span>{{interviewPersonMsg.email}}</span></li>
							</ul>
						</div>
					</div>
				</div>

				<!--应聘岗位信息-->
				<div>
					<div class="interviewTitle pr" @click="arrowPosterTitle=!arrowPosterTitle">
						<h4>应聘岗位信息 - {{interviewPosterMsg[1].value}}</h4>
						<img src="../../../static/images/arrowDown.png" alt="" width="20" class="pa" v-show="arrowPosterTitle" />
						<img src="../../../static/images/arrowUp.png" alt="" width="20" class="pa" v-else/>
					</div>
					<div class="interviewMain" v-show="arrowPosterTitle">
						<div>
							<ul>

								<li v-for="item in interviewPosterMsg"><span>{{item.name}}</span><span>{{item.value}}</span></li>
							</ul>
						</div>
					</div>
				</div>
				<!--发起人信息-->
				<div>
					<div class="interviewTitle pr" @click="arrowStartTitle=!arrowStartTitle">
						<h4>发起人信息 - {{interviewStartPerson[0].value}}</h4>
						<img src="../../../static/images/arrowDown.png" alt="" width="20" class="pa" v-show="arrowStartTitle" />
						<img src="../../../static/images/arrowUp.png" alt="" width="20" class="pa" v-else/>
					</div>
					<div class="interviewMain" v-show="arrowStartTitle">
						<div>
							<ul>

								<li v-for="item in interviewStartPerson"><span>{{item.name}}</span><span>{{item.value}}</span></li>
							</ul>
						</div>
					</div>
				</div>
				<!--面试安排-->
				<div>
					<div class="interviewTitle pr" @click="arrowArrangeTitle=!arrowArrangeTitle">
						<h4>面试安排</h4>
						<img src="../../../static/images/arrowDown.png" alt="" width="20" class="pa" v-show="arrowArrangeTitle" />
						<img src="../../../static/images/arrowUp.png" alt="" width="20" class="pa" v-else/>
					</div>
					<div class="interviewMain interviewArrange" v-for="item in interviewArrange" v-show="arrowArrangeTitle">
						<div>
							<ul>
								<li class="clearfix"><span>面试官</span><span>{{item.interviewArrangeMain[0].value}}</span></li>
								<li class="clearfix"><span>面试类型</span><span>{{item.interviewArrangeMain[1].value}}</span></li>
								<li class="clearfix"><span>面试地点</span><span>{{item.interviewArrangeMain[2].value}}</span></li>
								<li class="clearfix"><span>面试时间</span><span>{{item.interviewArrangeMain[3].value}}</span></li>
								<li class="clearfix"><span>面试状态</span><span>{{item.interviewArrangeMain[4].value}}</span></li>
							</ul>
						</div>
					</div>
				</div>
				<!--面试情况-->
				<div id="interviewSituation">
					<div class="interviewTitle pr" @click="arrowSituationTitle=!arrowSituationTitle">
						<h4>面试情况</h4>
						<img src="../../../static/images/arrowDown.png" alt="" width="20" class="pa" v-show="arrowSituationTitle" />
						<img src="../../../static/images/arrowUp.png" alt="" width="20" class="pa" v-else/>
					</div>
					<div class="interviewMain" v-show="arrowSituationTitle">
						<div class="clearfix">
							<div class="interviewMainBox">
								<div style="margin: 15px 0 0 60px;" v-for="item in interviewSituation">
									<ul class="pr">
										<div class="pa" style="top: 12px;left: -53px;">
											<img src="../../../static/images/correct.png" width="30" v-show="interviewSituation[$index].situationStatus" />
											<img src="../../../static/images/stop.png" width="30" v-else/>
										</div>
										<li><span>环节</span><span>{{item.link}}</span></li>
										<li><span>审批人</span><span>{{item.approvePerson}}</span></li>
									</ul>
									<div v-show="interviewSituation[$index].situationStatus">
										<div class="loadMore" @click="controlMore($index)" v-if="$index!=moreIndex">
											加载详情
										</div>
										<div class="loadMore" @click="controlMore($index)" v-else>
											收起详情
										</div>

										<div style="width: 100%;">
											<div class="moreMsg" v-show="$index==moreIndex">
												<ul>
													<li><span>员工类型： </span><span>{{item.loadMore[0].value}}</span></li>
													<li><span>建议职级： </span><span>{{item.loadMore[1].value}}</span></li>
													<li><span>安排职位： </span><span>{{item.loadMore[2].value}}</span></li>
													<li><span>建议子等： </span><span>{{item.loadMore[3].value}}</span></li>
													<li><span>专业知识： </span><span>{{item.loadMore[4].value}}</span></li>
													<li><span>专业技能： </span><span>{{item.loadMore[5].value}}</span></li>
													<li><span>用户： </span><span>{{item.loadMore[6].value}}</span></li>
													<li><span>好学： </span><span>{{item.loadMore[7].value}}</span></li>
													<li><span>实干： </span><span>{{item.loadMore[8].value}}</span></li>
													<li><span>评语： </span><span>{{item.loadMore[9].value}}</span></li>
													<li><span>风险不足： </span><span>{{item.loadMore[10].value}}</span></li>
													<li><span>笔试方向： </span><span>{{item.loadMore[11].value}}</span></li>
													<li><span>面试结果： </span><span>{{item.loadMore[12].value}}</span></li>
													<li><span>综合评价： </span><span>{{item.loadMore[13].value}}</span></li>
													<li><span>填写人： </span><span>{{item.loadMore[14].value}}</span></li>
													<li><span>填写日期： </span><span>{{item.loadMore[15].value}}</span></li>
													<div style="text-align: center;">
														<div class="closeMore" @click="moreIndex=-1">收起详情</div>
													</div>
												</ul>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div>
			<slot></slot>
		</div>
		<!--审批过程-->
		<div>
			<div class="approveProcess">
				<div class="hrdApprove">
					<div class="hrdApproveTitle interviewTitle pr" @click="arrowApproveTitle=!arrowApproveTitle">
						<h4>审批过程</h4>
						<img src="../../../static/images/arrowDown.png" alt="" width="20" class="pa" v-show="arrowApproveTitle" />
						<img src="../../../static/images/arrowUp.png" alt="" width="20" class="pa" v-else/>
					</div>
				</div>
				<div class="" v-show="arrowApproveTitle">
					<div class="approveMain">
						<div class="clearfix approveMainBox" v-for="item in approveProcess">
							<div class="fl" style="width: 30px;height: 30px;border-radius: 50%;border: 1px solid rgba(204, 204, 204, 1);text-align: center;line-height: 30px;">{{item.number}}

							</div>
							<div class="fl step">
								<ul>
									<li class="clearfix">
										<span>步骤名称</span>
										<span>{{item.approveMain[0].value}}</span>
									</li>
									<li class="clearfix">
										<span>处理时间</span>
										<span>{{item.approveMain[1].value}}</span>
									</li>
									<li class="clearfix">
										<span>详情</span><span>{{item.approveMain[2].value}}</span>
									</li>
									<li class="clearfix" v-show="item.processStatus">{{item.approveMain[3].value}}</li>
									<div class="loadMore" style="" @click="load(item.number, item.processStatus)" v-show="!item.processStatus">
										加载详情
									</div>
									<div class="loadMore" style="" @click="load(item.number, item.processStatus)" v-else>
										收起详情
									</div>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script lang="babel">
	export default{
		name:'interview-public',
		data:function(){
			return {
				//审批过程控制加载详情
				loadMoreShow:true,
				//应聘人信息
				arrowTitle:true,
				//应聘岗位信息
				arrowPosterTitle:true,
				//发起人信息
				arrowStartTitle:true,
				//面试安排信息
				arrowArrangeTitle:true,
				//面试情况信息
				arrowSituationTitle:true,
				//审批过程
				arrowApproveTitle:true,
				//此处为面试详情控制加载详情的变量
				moreIndex:-1,
				moreShow:false,
				//面试人的信息
				interviewPersonMsg:{
					name:'',
					sex:'',
					tel:'',
					email:''
				},
				//应聘岗位信息
				interviewPosterMsg:[
					{
						"name":"应聘部门",
						"value":''
					},
					{
						"name":"应聘岗位",
						"value":''
					},{
						"name":"需求职级",
						"value":''
					},{
						"name":"对应岗位",
						"value":''
					},
					{
						"name":"对应需求",
						"value":''
					}
				],
				interviewStartPerson:[
					{
						"name":"面试发起人",
						"value":''
					},
					{
						"name":"发起人时间",
						"value":''
					}
				],
				//面试安排
				interviewArrange:[],
				//面试情况
				interviewSituation:[],
				//审批过程
				approveProcess:[]
			}
		},
		methods:{
			load:function(id,bool){
				for(var i=0; i<this.approveProcess.length; i++){
					if (this.approveProcess[i].number === id){
						this.approveProcess[i].processStatus = !bool;
					}
				}
			},
			getInterviewMsg:function(response){
				this.interviewPersonMsg.name=response.data.interviewMsg[0].name;
				this.interviewPersonMsg.sex=response.data.interviewMsg[0].sex;
				this.interviewPersonMsg.tel=response.data.interviewMsg[0].tel;
				this.interviewPersonMsg.email=response.data.interviewMsg[0].email;
				for(var i=0;i<response.data.interviewMsg[0].interviewPosterMsg.length;i++){
					this.interviewPosterMsg[i].value=response.data.interviewMsg[0].interviewPosterMsg[i].value;
				}
				for(var i=0;i<response.data.interviewMsg[0].interviewStartPersonMsg.length;i++){
					this.interviewStartPerson[i].value=response.data.interviewMsg[0].interviewStartPersonMsg[i].value;
				}
				for(var i=0;i<response.data.interviewMsg[0].interviewArrange.length;i++){
					this.interviewArrange.push(response.data.interviewMsg[0].interviewArrange[i])
				}
				//面试情况的循环
				for(var i=0;i<response.data.interviewMsg[0].interviewSituation.length;i++){
					this.interviewSituation.push(response.data.interviewMsg[0].interviewSituation[i])
				}
				//审批过程的循环
				for(var i=0;i<response.data.interviewMsg[0].approveProcess.length;i++){
					this.approveProcess.push(response.data.interviewMsg[0].approveProcess[i])
				}
			},
			controlMore:function(index){
				if(this.moreIndex!=index){
					this.moreIndex=index;
				}else{
					this.moreIndex=-1;
				}
			}
		},
		ready:function() {
			this.$http.get('static/data/interviewDataMsg.json').then((response) => {
				response.data = $.parseJSON(response.data) == null? response.data: $.parseJSON(response.data);
				this.getInterviewMsg(response);
			}, (response) => {});
			
		}
	}


