<style scoped lang="sass">
	div.replace {
		position: fixed;
		z-index: 4;
		top: 0;
		width: 100%;
	}
	
	.rejectCon {
		margin-top: 10px;
		.rejectPerson {
			.rejectTo {
				background-color: #fff;
				border-top: 1px solid rgba(204, 204, 204, 1);
			}
			h4 {
				border-bottom: 1px solid rgba(204, 204, 204, 1);
				padding-bottom: 5px;
				font-size: 14px;
				font-weight: normal;
				padding: 10px 0 10px 20px;
			}
			.rejectReason {
				width: 100%;
				h4 {
					padding-top: 20px;
					border: none;
				}
				div {
					margin: 0 auto;
					width: 95%;
				}
			}
			input,
			textarea {
				outline: none;
				border: none;
				resize: none;
				width: 100%;
				border-radius: 5px;
			}
		}
	}
	
	.rejectBtn {
		margin-top: 40px; 
		width: 100%;
		.btn {
			width: 100%;
			height: 50px;
			font-size: 16px;
		}
		div {
			margin: 0 auto;
			width: 96%;
		}
		div:nth-of-type(1) {
			.btn {
				background: #30a6e4;
				color: #fff;
			}
		}
		div:nth-of-type(2) {
			margin-top: 20px;
			.btn {
				border: 1px solid #30a6e4;
				color: #30a6e4;
			}
		}
	}
	/*提交成功失败*/
	
	span.block {
		height: 95px;
		text-align: center;
		line-height: 95px;
	}
	
	.Btnfail {
		width: 50%;
		border-top: 1px solid #666;
		height: 40px;
		text-align: center;
		line-height: 40px;
	}
	
	.Btnfail:nth-of-type(1) {
		box-sizing: border-box;
		border-right: 1px solid #666;
	}
</style>
<template>
	<div v-show="backReplace" id='idd' class="replace" :style="{'height':height+'px'}">
		<div class="rejectCon">
			<div class="rejectPerson">
				<div class="rejectTo">
					<h4 class="pr"><img src="../../../static/images/star.png" width="10" />
						<label for="">处理人</label>
						<i class="pa" style="right: 20px;top: 13px;"><img :src="gt.gtImg" :alt="gt.gtText" width="10"/></i>
						<input type="text" placeholder="请输入新的处理人" style="width: 50%;margin-top: 2px;text-align: right;margin-right: 40px;" v-model="submitData.replacePerson"
							@click="setOpenChooser('user')" class="fr" />
					</h4>
				</div>
				<div class="rejectReason">
					<h4>
						<img src="../../../static/images/star.png" width="10" /> 更换原因
					</h4>
					<div>
						<textarea name="" rows="12" cols="" placeholder="请输入原因" v-model="submitData.replaceReason" @click="setScroll()"></textarea>
					</div>
				</div>
			</div>
		</div>
		<div class="rejectBtn">
			<div><button class="btn" @click="submitBtn($event)">提交</button></div>
			<div><button class="btn" @click="backBtn">返回</button></div>
		</div>
		<!--检测浮层-->
		<div class="inputPoint" v-show="inputPointShow">
			<div class="inputPointMsg ">
				请将必填项填写完整
			</div>
		</div>
		<div v-show="showfloat">
			<div v-if="success" class="successBtn" :style="widths">
				<button class="btn btnSuccess">操作成功</button>
			</div>
			<div v-else class="failBtn">
				<div>
					<span class="block">操作失败，请检查网络后重试。</span>
				</div>
				<div class="clearfix">
					<span class="fl Btnfail" @click="showfloat=false">取消</span>
					<span class="fl Btnfail" @click="submitBtn">重试</span>
				</div>
			</div>
		</div>
		<chooser :show.sync="isOpenChooser" :chooser-type="chooserType" @choosers="getChooserId">
		</chooser>
	</div>
</template>
<script lang="babel">
	import chooser from '../common/chooser.vue'
	export default{
		name:'reject',
		components:{chooser},
		data:function(){
			return{
				// 页面高度
				height: '',
				idd:'',
				success:true,
				showfloat:false,
				userName:"",
                // 当前操作人id
                currentUserId: -1,
				//
				isOpenChooser:false,
				widths:'',
				gt:{
					gtImg:'static/images/gt.png',
					gtText:'大于号'
				},
				// 当前弹出框类型
				chooserType: '',
				//信息填写不完全的提示
				inputPointShow:false,
				//replaceData
				submitData:{
					replacePerson:'',
					replacePersonId:'',
					replaceReason:''
				}
				
			}
		},
		props:{
			backReplace:{
				type:Boolean,
				default:true,
				twoWay:true
			}
		},
		ready: function () {
			windowGlobal.disableScroll();
			this.getWindowGlobal();
		},
		methods:{
			// 设置弹出层高度
			getWindowGlobal: function () {
				this.height = window.screen.height;
			},
			setPersonal: function () {
				this.isOpenChooser=true;
			},
			//提交按钮
			submitBtn:function(){
				if(this.submitData.replacePerson==""||this.submitData.replaceReason==""){
					this.inputPointShow=true;
					var that = this;
					setTimeout(function(){
						that.inputPointShow=false;
					},2000)
				} else {
      				this.$emit('replace', this.submitData);
      				this.backReplace = false;
				}
				windowGlobal.enableScroll();
		        this.backReplace = false;
			},
			//返回按钮
			backBtn:function(){
				this.backReplace=false;
				windowGlobal.enableScroll();
			},
			// 打开选择器弹窗
			setOpenChooser: function (typeName) {
				this.chooserType = typeName;
				this.isOpenChooser =true;
				windowGlobal.myScroll();				
			},
			// 获取当前选择器选择的对象
			getChooserId: function (msg) {
				this.submitData.replacePersonId = msg.id;
				this.submitData.replacePerson = msg.name;
			},
			// 设置滚动
			setScroll: function () {
				windowGlobal.myScroll();
			}
		}
		// ,
  //       watch: {
  //           backReplace (value) {
  //               // 在显示时去掉body滚动条，防止出现双滚动条
  //               if (value) {
  //               	// $("body").bind("touchmove",function(event){event.preventDefault;});
  //                   document.body.className += ' modal-open';
  //               }
  //               // 在modal动画结束后再加上body滚动条
  //               else {
  //               	// $("body").unbind("touchmove");
  //                  document.body.className = document.body.className.replace(/\s?modal-open/, '');
  //               }
  //           }
  //       }
	}
</script>


