<style lang="sass" scoped>
	/*修改面试官选择器*/
	
	.chooser {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		background: rgba(255, 255, 255, 0.95);
		z-index: 9999;
		.search {
			padding: 10px;
			background: #F4F4F4;
			border-bottom: 1px solid #D9D9D9;
			position: relative;
			input {
				border: 1px solid #EDEDEF;
				border-radius: 5px;
				box-shadow: none;
				height: 30px;
				width: 85%;
				padding-left: 2%
			}
			.icon.btn-search {
				position: absolute;
				width: 20px;
				height: 20px;
				right: 18%;
				margin-top: 3px;
				background-position: -430px -16px;
			}
		}
		.content {
			padding: 0 10px;
			overflow: auto;
			li {
				border-bottom: 1px solid #D9D9D9;
				padding: 10px 0;
			}
		}
		.interview-submit {
			padding: 10px 0;
			background: #F3F3F3;
			text-align: center;
			position: fixed;
			bottom: 0;
			left: 0;
			width: 100%;
			z-index: 1;
			border-top: 1px solid #D3D3D3;
		}
		ul.users > li {
			overflow: auto;
			span {
				float: left;
			}
			a {
				float: right;
				display: inline-block;
				width: 20px;
				height: 20px;
			}
			.add {
				background-position: -467px -18px;
			}
			.reduce {
				background-position: -467px -38px;
			}
		}
		h1 {
			padding: 10px 20px;
			color: #2FA7E3;
			border-top: 1px solid #D3D3D3;
			border-bottom: 1px solid #D3D3D3;
		}
	}
</style>
<template>
	<div v-show="show">
		<div class="chooser" :style="{'height':height+'px'}">
			<div class="rightHead">
				<ul class="clearfix">
					<li>
						<a href="javascript:void(0)">面试官名单</a>
					</li>
				</ul>
			</div>
			<div class="search">
				<input type="text" name="" v-model="searchName" class="searchName" placeholder="请输入查询内容" @keyup="search">
				<!-- <a href="javascript:void(0)" class="icon btn-search pull-right" @click="search"></a> -->
				<a href="javascript:void(0)" class="link" @click="clearSearchName">取消</a>
			</div>
			<div class="content" :style="{'height':(height-200)/2 +'px'}">
				<ul class="users">
					<li v-for="item in dataList">
						<span v-text="item.name"></span>
						<a href="javascript:void(0)" class="icon add" @click="addInfo(item.id, item.name)"></a>
					</li>
				</ul>
			</div>
			<div class="content">
				<h1>确认名单</h1>
			</div>
			<div class="content" :style="{'height':(height-200)/2 +'px'}">
				<ul class="users">
					<li v-for="item in newUsers">
						<span v-text="item.operatUser"></span>
						<a href="javascript:void(0)" class="icon reduce" @click="deleteInfo(item.operatUserId)"></a>
					</li>
				</ul>
			</div>

			<!-- 底部按钮开始 -->
			<div class="interview-submit">
				<button type="button" class="btn btn-primary mar-right-fifty" @click="cancel">取消</button>
				<button type="button" class="btn btn-primary" @click="getConfirmation">确认名单</button>
			</div>
			<!-- 底部按钮结束 -->

			<!-- 提示框开始 -->
			<tip-box :tip-box-status.sync="tipsBox.isShow" :tip-title="tipsBox.tipTitle" :tip-content="tipsBox.tipContent" :tip-button="tipsBox.tipButton"
				:tip-confirm-btn="tipsBox.tipConfirmBtn" :tip-cancel-btn="tipsBox.tipCancelBtn" :tip-box-close.sync="tipsBox.tipBoxClose">
			</tip-box>
			<!--提示框结束-->
		</div>
	</div>
</template>
<script lang="babel">
	import tipBox from '../common/tipBox.vue'

	export default {
        props: {
            show: {
                type: Boolean,
                twoWay: true,
                default: false
            },
            users: {
                type: Array
            },
            idArrs: {
            	type: Array
            }
		},
		data: function () {
			return {
                // 提示框
                tipsBox: {
                    "isShow": false,
                    "tipContent": "内容",
                    "tipBoxClose": true,
                    "tipButton": 1,
                    "tipConfirmBtn": function () {
                    },
                    "tipCancelBtn": function () {
                    }
                },
				// 页面高度
				height: '',
				// 搜索名称
				searchName: '',
				// 查询的数据列表
				dataList: [],
				// 新增的ID数组
				newIdArrs: [],
				// 新增的user数组
				newUsers: []
			}
		},
		ready: function () {
			this.getWindowGlobal();
			this.setArrId();
			for (var i = 0; i < this.idArrs.length; i++) {
				this.newIdArrs.push(this.idArrs[i]);
			}
			for (var i = 0; i < this.users.length; i++) {
				this.newUsers.push(this.users[i]);
			}
			$(".searchName").focus();
		},
		components: {
			tipBox
		},
		methods: {
			// 设置弹出层高度
			getWindowGlobal: function () {
				this.height = window.screen.height;
			},
			// 获取列表数据
			search: function () {
				let params = {
	                'name': this.searchName
            	};
            	let typeName ='';
    			this.$http.get('static/data/staff.json', {'params': params}).then(function (res) {
            		res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                    this.dataList = res.data.data;
                });
			},
			// 取消
			cancel: function () {
				this.newUsers = this.users;
				this.newIdArrs = this.idArrs;
                this.$emit('cancel');
                this.show = false;
                this.clear();
			},
			// 清空数据
			clear: function () {
				this.searchName = '';
				this.dataList = '';
				this.newIdArrs = [];
			},
			// 添加数据到确认名单
			addInfo: function (id, name) {
				let obj = {};
					obj.operatUserId = id;
					obj.operatUser = name;

				if (this.newIdArrs.indexOf(id) === -1) {
					this.newIdArrs.push(id);
					this.newUsers.push(obj);
				}
			},
			// 删除确认名单数据
			deleteInfo: function (id) {
				for (var i = 0; i < this.newUsers.length; i++) {
					if(this.newUsers[i].operatUserId === id) {
						this.newUsers.splice(i, 1);
						var index = this.newIdArrs.indexOf(id);
						if (index > -1) {
							this.newIdArrs.splice(index, 1);
						}
					}
				}
			},
			// 点击确认名单按钮
			getConfirmation: function () {
				if (this.newUsers.length === 0) {
					this.tipsBox.tipBoxClose = true;
                    this.tipsBox.tipContent = "请选择确认名单！";
                    this.tipsBox.tipButton = 2;
                    this.tipsBox.isShow = true;
				} else {
					this.$emit('choosers', this.newUsers);
	                this.show = false;
	                this.clear();
				}
			},
			// 获取已有的面试官id添加到数组中
			setArrId: function () {
				if (this.newUsers.length > 0) {
					for (var i = 0; i < this.newUsers.length; i++) {
						this.newIdArrs.push(this.newUsers[i].operatUserId);
					}
				}
			},
			// 清空查询框数据
			clearSearchName: function () {
				this.searchName = '';
				this.dataList = [];
			}
		},
        watch: {
            show (value) {
                // 在显示时去掉body滚动条，防止出现双滚动条
                if (value) {
                    document.body.className += ' modal-open';
                }
                // 在modal动画结束后再加上body滚动条
                else {
                   document.body.className = document.body.className.replace(/\s?modal-open/, '');
                }
            }
        }
	}		
</script>


