<style lang="sass">
	div.speak,
	div.fr {
		width: 10%;
	}
	
	h4 {
		font-weight: normal;
	}
	
	.text {
		width: 75%;
		margin-left: 10px;
		position: relative;
		height: 40px;
		textarea,
		button {
			width: 100%;
			height: 40px;
			background: #fff;
			border-radius: 5px;
			border: 1px solid #acacb0;
			font-size: 16px;
			box-sizing: border-box;
			resize: none;
		}
		textarea::-webkit-input-placeholder {
			padding: 8px;
			font-size: 16px;
		}
		img {
			position: absolute;
			bottom: 150%;
			left: 25%;
		}
	}
	.shade {
		background:rgba(0,0,0,0);
		width:100%;
		height:100%;
		position:fixed;
		left:0;
		top:0;
		z-index:999;
	}
	.shade-img{
		z-index:1000;
		width:150px;
		height:150px;
		position:fixed;
		left:50%;
		margin-left:-75px;
	}
	.fl img{
		margin-top:3px;
	}
	.fr img{
		width:34px;
		height:34px;
		margin-top:3px;
	}
</style>
<template>
	<div class="clearfix">
		<div class="speak fl" @click="keyboardAndRecord"><img :src="broadAndHorn" alt="" width="33.5" /></div>
		<div style="position: absolute;width: 34px;height: 38px;top: 12px;left: 15px;" v-show="imgFloat"></div>
		<div class="fl text">
			<textarea @keyup='inputvalue' name="" v-show="textareaShow" placeholder="请输入内容" v-model="adviseInfo"></textarea>
			<button v-html="recordsHtml" v-else @click="recordSpeak"></button>
		</div>
		<div class="fr" v-show="hornShow" @click="hornImgShow=!hornImgShow">
			<img src="../../../static/images/horn.png" v-show="hornImgShow" />
			<!--这里是点击播放语音的另一张图片，暂时用键盘代替-->
			<img src="../../../static/images/horning.gif" v-else/>
		</div>

		<!-- 录音状态开始 -->
		<img src="../../../static/images/recording.gif" class="shade-img" alt="" v-show="recordImgShow" :style="{'top': ((height - 150)/2 - 20) + 'px'}" @click="recordSpeak()" />
		<div class="shade" :style="{'height': height + 'px'}" v-show="recordImgShow"> </div>
		<!-- 录音状态结束 -->
	
		<!-- 语音播放开始 -->
		<div class="shade" :style="{'height':height+'px'}" v-show="!hornImgShow" @click="hornImgShow=!hornImgShow"> </div>
		<!-- 语音播放结束 -->

	</div>

</template>
<script lang="babel">
	export default {
		name: 'approveAdvise',
		data: function() {
			return {
				// 页面高度
				height: '',
				// 屏幕高度
				screenHeight: '',
				textareaShow: 'true',
				//图片浮层
				imgFloat:false,
				//右侧录音按钮
				hornShow: false,
				//右侧录音播放按钮
				hornImgShow:true,
				//录音功能			 	
				records: {
					speak: '按住说话',
					record: '停止录音',
					recordAgain: '点此重新录音'
				},
				//监控语音
				number:0,
				recordsHtml: '',
				status: true,
				//正在录音出现
				recordImgShow: false,
				
				//键盘和录音切换
				keybroadAndHorn: {
					keyBroad: 'static/images/keyBroad.png',
					broadTurnHorn: 'static/images/broadTurnHorn.png'
				},
				broadAndHorn: '',
				broadStatus: true,
				//语音变量接口
				languageMsg:'这里是语音变量的接口'
			}
		},
		props: {
			adviseInfo: {
				type: String
			}
		},
		methods: {
			// 设置弹出层高度
			getWindowGlobal: function () {
				this.height =  window.screen.height;
			},
			inputvalue: function () {
				// this.$dispatch('advise-msg', this.adviseInfo);
				this.$emit('approve', this.adviseInfo);
			},
			//点击键盘切换
			keyboardAndRecord: function() {
				this.textareaShow = !this.textareaShow;				
				if(this.broadStatus == true) {
					this.broadAndHorn = this.keybroadAndHorn.keyBroad;
					this.broadStatus = false;
					//右侧喇叭出现
					if (this.number==1 || this.number==2) {
						this.hornShow = true;
					}
				} else {
					this.broadAndHorn = this.keybroadAndHorn.broadTurnHorn;					
					this.broadStatus = true;
					//右侧喇叭消失
					if (this.number==1 || this.number==2) {
						this.hornShow = false;
					}
				}
			},
			//点击录音切换
			recordSpeak: function() {
				this.adviseInfo=this.languageMsg;
				
				if(this.number==0){
					// 开始录音
					windowGlobal.disableScroll();
					this.recordsHtml = this.records.record;
					this.recordImgShow = true;
					this.imgFloat=true;
					this.number++;

					this.$http.get('api/startRecord').then(function(res){
						res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
						if(res.data.Result==true){
							console.log('开始录音');
						}
					}).catch(function (res) {
						console.log('录音失败');
	                });
				} else if(this.number==1){
					// 停止录音
					windowGlobal.enableScroll();
					this.recordsHtml = this.records.recordAgain;
					this.imgFloat=false;
					this.recordImgShow = false;
					this.number++;
					this.hornShow = true;

					this.$http.get('api/startRecord').then(function(res){
						res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
						if(res.data.Result==true){
							console.log('停止录音');
						}
					}).catch(function (res) {
						console.log('录音失败');
	                });
				} else if(this.number==2){
					// 点此重新录音
					windowGlobal.enableScroll();
					this.recordsHtml = this.records.record;
					this.recordImgShow = true;
					this.imgFloat=true;
					this.number=1;
					this.hornShow = true;
					this.$http.get('api/startRecord').then(function(res){
						res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
						if(res.data.Result==true){
							console.log('点此重新录音');
						}
					}).catch(function (res) {
						console.log('录音失败');
	                });
				}
			}
		},
		ready: function() {
			this.recordsHtml = this.records.speak;
			this.broadAndHorn = this.keybroadAndHorn.broadTurnHorn;
			this.getWindowGlobal();
		}
	}
</script>


