<style scoped lang="sass">
	div.replace {
		position: fixed;
		z-index: 4;
		top: 0;
		width: 100%; 
	}
	
	.rejectCon {
		margin-top: 10px;
		.rejectPerson {
			.rejectTo {
				background-color: #fff;
				border-top: 1px solid rgba(204, 204, 204, 1);
			}
			h4 {
				border-bottom: 1px solid rgba(204, 204, 204, 1);
				padding-bottom: 5px;
				font-size: 14px;
				font-weight: normal;
				padding: 10px 0 10px 20px;
			}
			.rejectReason {
				width: 100%;
				h4 {
					padding-top: 20px;
					border: none;
				}
				div {
					margin: 0 auto;
					width: 95%;
				}
			}
			input,
			textarea {
				outline: none;
				border: none;
				resize: none;
				width: 100%;
				border-radius: 5px;
			}
		}
	}
	
	.rejectBtn {
		margin-top: 40px; 
		width: 100%;
		.btn {
			width: 100%;
			height: 50px;
			font-size: 16px;
		}
		div {
			margin: 0 auto;
			width: 96%;
		}
		div:nth-of-type(1) {
			.btn {
				background: #30a6e4;
				color: #fff;
			}
		}
		div:nth-of-type(2) {
			margin-top: 20px;
			.btn {
				border: 1px solid #30a6e4;
				color: #30a6e4;
			}
		}
	}
	/*提交成功失败*/
	
	span.block {
		height: 95px;
		text-align: center;
		line-height: 95px;
	}
	
	.Btnfail {
		width: 50%;
		border-top: 1px solid #666;
		height: 40px;
		text-align: center;
		line-height: 40px;
	}
	
	.Btnfail:nth-of-type(1) {
		box-sizing: border-box;
		border-right: 1px solid #666;
	}
</style>
<template>
	<div v-show="backAbandon" id='idd' class="replace" :style="{'height':height+'px'}">
		<div class="rejectCon">
			<div class="rejectPerson">
				<div class="rejectReason">
					<h4>
						<img src="../../../static/images/star.png" width="10" /> 放弃原因
					</h4>
					<div><textarea name="" rows="12" cols="" placeholder="请输入原因" v-model="submitData.abandonReason" @click="setScroll()"></textarea></div>
				</div>

			</div>
		</div>
		<div class="rejectBtn">
			<div><button class="btn" @click="submitBtn($event)">提交</button></div>
			<div><button class="btn" @click="backBtn">返回</button></div>
		</div>

		<!--检测浮层-->
		<div class="inputPoint" v-show="inputPointShow">
			<div class="inputPointMsg ">
				请将必填项填写完整
			</div>
		</div>

		<div v-show="showfloat">
			<div v-if="success" class="successBtn" :style="widths">
				<button class="btn btnSuccess">操作成功</button>
			</div>
			<div v-else class="failBtn">
				<div>
					<span class="block">操作失败，请检查网络后重试。</span>
				</div>
				<div class="clearfix">
					<span class="fl Btnfail" @click="showfloat=false">取消</span>
					<span class="fl Btnfail" @click="submitBtn">重试</span>
				</div>
			</div>
		</div>
	</div>
</template>
<script lang="babel">
	export default{
		data:function(){
			return {
				// 页面高度
				height: '',
				idd:'',
				inputPointShow:false,
				showfloat:false,
				success:false,
				submitData:{
					abandonReason:''
				}				
			}
		},
		props:{
			backAbandon:{
				type:Boolean,
				default:true,
				twoWay:true
			}
		},
		ready: function () {
			windowGlobal.disableScroll();
			this.getWindowGlobal();
		},
		methods:{
			// 设置弹出层高度
			getWindowGlobal: function () {
				this.height = window.screen.height;
			},
			//返回按钮
			backBtn:function(){
				this.backAbandon=false;
				windowGlobal.enableScroll();
			},
			//点击提交按钮
			submitBtn:function(e){
				if(this.submitData.abandonReason==""){
					this.inputPointShow=true;
					var that=this;
					setTimeout(function(){
						that.inputPointShow=false;
					},2000)
				} else {					
      				this.$emit('abandon', this.submitData);
      				this.backAbandon = false;
				}
				windowGlobal.enableScroll();
			},
			// 设置滚动
			setScroll: function () {
				windowGlobal.myScroll();
			}
		}
	}
</script>


