<style scoped lang="sass">
	.profess {
		.profess-tip {
			padding: 7px 5%;
			color: #9C9C9A;
		}
		.process-title {
			padding: 10px 3%;
			color: #979797;
			border-top: 1px solid #D3D3D3;
			border-bottom: 1px solid #D3D3D3;
			background: #fff;
			margin-top: 30px;
			span {
				margin-right: 10px;
				color: #202020;
			}
		}
		.star {
			width: 16px;
			height: 18px;
			display: inline-block;
			vertical-align: top;
			background-position: -503px -15px;
		}
		.require {
			li {
				padding: 10px 0px 10px 3%;
				border-top: 1px solid #D3D3D3;
				border-bottom: 1px solid #D3D3D3;
				background: #fff;
				margin-bottom: 5px;
				position: relative;
				.name {
					color: #878787;
					width: 19%;
					display: inline-block;
					vertical-align: top;
				}
				.value {
					color: #202020;
					width: 60%;
					display: inline-block;
					text-align: right;
				}
				.icon-user {
					z-index: 2;
					width: 20px;
					height: 20px;
					position: absolute;
					right: 3%;
					background-position: -617px -18px;
				}
				.input-user.inter-mode {
					overflow: hidden;
					position: relative;
					color: #202020;
					width: 71%;
					display: inline-block;
					text-align: right;
					em {
						position: absolute;
						width: 21px;
						height: 20px;
						left: 0;
						top: 0;
						display: inline-block;
						background: #fff;
					}
					select {
						width: 100%;
						direction: rtl;
						border: 0;
						padding-right: 16px;
						option {
							direction: rtl;
						}
					}
				}
			}
		}
		.process-evaluate {
			h1 {
				padding: 10px 3%;
				strong {
					color: #969694;
					font-weight: normal;
					margin-left: 10px;
				}
				a {
					float: right;
				}
			}
			.process-ul {
				width: 295px;
				margin: 10px auto 0px auto;
				li {
					clear: both;
					padding-bottom: 5px;
					overflow: auto;
					height: 40px;
					span {
						display: inline-block;
						margin-bottom: 0px;
						margin-right: 6%;
						width: 9%;
						text-align: center;
						height: 30px;
						float: left;
					}
					span:last-child {
						margin-right: 0;
					}
					.name {
						width: 32px;
						font-size: 12px;
						vertical-align: middle;
						color: #969694;
						&.left {
							line-height: 30px;
						}
						&.top {
							width: 13%;
							margin-right: 2.5%;
						}
					}
					.score {
						width: 30px;
						height: 30px;
						border: 1px solid #BFBEC3;
						border-radius: 5px;
						&.active {
							background: #30A6E4;
							border-color: #30A6E4;
						}
					}
				}
			}
		}
		.process-block {
			padding: 10px 3%;
			border-top: 1px solid #D3D3D3;
			margin-top: 10px;
			&.result {
				color: #1E1E1E;
				padding-left: 31px;
				span {
					padding-right: 10px;
					color: #868686;
				}
			}
		}
	}
	
	.scoreInstruction {
		z-index: 2000;
		top: 0;
		left: 0;
		width: 100%;
		background: rgba(0, 0, 0, 0.5);
		position: absolute;
		height: 100%;
		.line {
			width: 1px;
			background: #fff;
			height: 39px;
			bottom: -9%;
			left: 50%;
		}
		.closeInstruction {
			bottom: -88px;
			left: 50%;
			width: 50px;
			height: 50px;
			border-radius: 50%;
			border: 1px solid #fff;
			color: #fff;
			font-size: 16px;
			margin-left: -25px;
			img {
				display: block;
				width: 32px;
				margin: 8px auto;
			}
		}
		.scoreInstruction-wrap {
			top: 7%;
			left: 50%;
			position: fixed;
			width: 80%;
			background: #fff;
			border-radius: 10px;
			margin-left: -40%;
			padding: 10px;
		}
		.scoreInstructionMain {
			position: relative;
			overflow: auto;
			height: 400px;
			p {
				padding: 20px 15px 0 15px;
				margin-bottom: 25px;
			}
			h1 {
				border-bottom: 1px solid #C1C1C1;
				padding-bottom: 10px;
			}
			ul {
				overflow: auto;
				li {
					float: left;
					width: 50%;
					line-height: 25px;
					label,
					span {
						display: inline-block;
					}
					label {
						color: #9B9B9B;
						margin-right: 10px;
					}
				}
			}
			h2 {
				clear: both;
				padding-bottom: 5px;
				margin-top: 5px;
			}
			table {
				clear: both;
				th {
					font-weight: normal;
					background: #E4E4E4;
					height: 40px;
				}
				td,
				th {
					text-align: center;
					font-size: 12px;
				}
				td {
					background: #F4F4F4;
					&.col {
						text-align: left;
						padding: 2px 3px;
					}
				}
			}
		}
	}
	/* 按钮系列样式 */
	
	div.buttonSeries {
		background-color: #fff;
		position: fixed;
		bottom: 0;
		width: 100%;
		z-index: 3;
		.buttonSeriesBox {
			padding: 8px 4px 8px 6px;
			height: auto;
		}
		.btn {
			border-color: #30a6e4;
			color: #30A6E4;
			height: 40px;
			min-width: 10%;
			width: 20%;
			padding: 6px 2%;
		}
		.btn1 {
			width: 35%;
		}
		div.btnMore {
			border-radius: 5px;
			border: 1px;
		}
	}
	
	.submit {
		width: 22%;
	}
	
	.advise {
		margin-top: 10px;
	}
</style>

<template>
	<div>
		<!-- 导航开始 -->
		<right-head>
			<li>
				<a>招聘选拔 </a>
				<span>&gt;</span>
				<a>社会招聘</a>
				<span>&gt;</span>
				<a>面试流程跟踪</a>
				<span>&gt;</span>
				<a>专业初试</a>
			</li>
		</right-head>
		<!-- 导航结束 -->
		<div class="hrd-authorize">
			<interview-public>
				<div class="profess">
					<!--专业初试内容开始-->
					<h1 class="process-title">
						<span>
							专业初试
						</span> 标
						<i class="icon star"></i>为必填项
					</h1>
					<div class="profess-tip">
						<p>
							请谨慎填写内容！
						</p>
						<p>
							提交后，如需修改，需等到薪资谈判环节才能驳回到原环节进行修改
						</p>
					</div>
					<ul class="require">
						<li>
							<em class="icon star"></em>
							<span class="name">员工类型</span>
							<div class="input-user inter-mode">
								<label style="margin-right:13px;">正式</label>
							</div>
						</li>
						<li>
							<em class="icon star"></em>
							<span class="name">职位体系</span>
							<div class="input-user inter-mode">
								<em></em>
								<select v-model='hintData.checkedNames'>
									<option value="" selected>请选择</option>
		                            <option value="1">腾讯正式员工</option>
		                            <option value="2">腾讯商业子公司正式员工(S系统专用)</option>
		                        </select>
							</div>
							<em class="icon icon-user"></em>
						</li>
						<li>
							<em class="icon star"></em>
							<span class="name">职位类</span>
							<div class="input-user inter-mode">
								<em></em>
								<select v-model='hintData.position'>
									<option value="" selected>请选择</option>
		                            <option v-for='item in interviewComment.positionClass'>{{item.type}}</option>
		                        </select>
							</div>
							<em class="icon icon-user"></em>
						</li>
						<li>
							<em class="icon star"></em>
							<span class="name">安排职位</span>
							<div class="input-user inter-mode">
								<em></em>
								<select v-model='hintData.arrange'>
									<option value="" selected>请选择</option>
	                                <option v-for='item in interviewComment.arrangePosition'>{{item.type}}</option>
	                            </select>
							</div>
							<em class="icon icon-user"></em>
						</li>
						<li>
							<em class="icon star"></em>
							<span class="name">建议职级</span>
							<div class="input-user inter-mode">
								<em></em>
								<select v-model='hintData.rank'>
									<option value="" selected>请选择</option>
		                            <option v-for='item in interviewComment.recommendRank'>{{item.type}}</option>
		                        </select>
							</div>
							<em class="icon icon-user"></em>
						</li>
						<li>
							<em class="icon star"></em>
							<span class="name">建议子等</span>
							<div class="input-user inter-mode">
								<em></em>
								<select v-model='hintData.subset'>
									<option value="" selected>请选择</option>
		                            <option v-for='item in interviewComment.recommendSubset'>{{item.type}}</option>
		                        </select>
							</div>
							<em class="icon icon-user"></em>
						</li>
					</ul>
					<!-- 专业初试内容结束 -->

					<!--面试评价内容开始-->
					<h1 class="process-title">
						<span>
							面试评价
						</span> 标
						<i class="icon star"></i>为必填项
					</h1>
					<div class="process-evaluate">
						<h1>
							<em class="icon star"></em>
							<span>面试评分</span>
							<strong>{{total}}分</strong>
							<a href="javascript:void(0)" class="link" @click="isWeightDescription = true">说明</a>
						</h1>
						<ul class="process-ul">
							<li>
								<span class="name"> </span>
								<span class="name two">远超要求</span>
								<span class="name two">超出要求</span>
								<span class="name two">符合要求</span>
								<span class="name top">未达到要求</span>
								<span class="name top">远不及要求</span>
							</li>
							<li v-for="item in interviewComment.score">
								<span class="name two" :class="{'left': $index > 1}" v-text="item.project"></span>
								<span class="score" :class="{'active': auditionScore[$index].score == 5}" @click='mark($index, 5)'></span>
								<span class="score" :class="{'active': auditionScore[$index].score == 4}" @click='mark($index, 4)'></span>
								<span class="score" :class="{'active': auditionScore[$index].score == 3}" @click='mark($index, 3)'></span>
								<span class="score" :class="{'active': auditionScore[$index].score == 2}" @click='mark($index, 2)'></span>
								<span class="score" :class="{'active': auditionScore[$index].score == 1}" @click='mark($index, 1)'></span>
							</li>
						</ul>
					</div>
					<!-- 面试评价内容结束 -->

					<!-- 面试评语开始 -->
					<div class="process-block">
						<h1>
							<em class="icon star"></em>
							<span>面试评语</span>
						</h1>
						<div class="advise">
							<approve-advise :advise-info="hintData.comments" @approve="getComments"></approve-advise>
						</div>
					</div>
					<!-- 面试评语结束 -->

					<!-- 风险不足开始 -->
					<div class="process-block">
						<h1>
							<em class="icon star"></em>
							<span>风险不足</span>
						</h1>
						<div class="advise">
							<approve-advise :advise-info="hintData.risk" @approve="getRisk"></approve-advise>
						</div>
					</div>
					<!-- 风险不足结束 -->

					<!-- 笔试方向内容及成绩开始 -->
					<div class="process-block">
						<h1>
							<span>笔试方向及成绩</span>
						</h1>
						<div class="advise">
							<approve-advise :advise-info="hintData.achievement" @approve="getAchievement"></approve-advise>
						</div>
					</div>
					<!-- 笔试方向内容及成绩结束 -->
					<div class="process-block result">
						<span>面试结果</span>{{total}}分
					</div>
					<!-- 综合评价开始 -->
					<ul class="require">
						<li>
							<em class="icon star"></em>
							<span class="name">综合评价</span>
							<div class="input-user inter-mode">
								<em></em>
								<select v-model='hintData.evaluate'>
									<option value="" selected>请选择</option>
		                            <option value="1">强烈推荐</option>
		                            <option value="2">推荐</option>
		                            <option value="3">不推荐</option>
		                        </select>
							</div>
							<em class="icon icon-user"></em>
						</li>
					</ul>
					<!-- 综合评价结束 -->
				</div>
			</interview-public>
		</div>
	</div>

	<!-- 权重说明开始 -->
	<div class="pa scoreInstruction" v-show="isWeightDescription">
		<div class="scoreInstruction-wrap">
			<div class="scoreInstructionMain">
				<h1>权重说明</h1>
				<ul>
					<li>
						<label>专业知识</label>
						<span>25%</span>
					</li>
					<li>
						<label>专业技能</label>
						<span>25%</span>
					</li>
					<li>
						<label>用户</label>
						<span>15%</span>
					</li>
					<li>
						<label>好学</label>
						<span>10%</span>
					</li>
					<li>
						<label>实干</label>
						<span>15%</span>
					</li>
				</ul>
				<h2>专业知识</h2>
				<table>
					<tr>
						<th style="width:40%">评估要点</th>
						<th style="width:60%">关键行为</th>
					</tr>
					<tr>
						<td>设定有挑战的目标</td>
						<td class="col">为自己和他人设定有挑战的目标，以取得卓越的业务成果。</td>
					</tr>
					<tr>
						<td>持续专注于目标</td>
						<td class="col">集中精力，全情投入，始终专注于目标的达成。</td>
					</tr>
					<tr>
						<td>克服障碍</td>
						<td class="col">
							1、坚持不懈地努力以克服阻碍，实现或超越目标。<br> 2、从实现挑战性目标中获得满足感。
						</td>
					</tr>
					<tr>
						<td>持续优化</td>
						<td class="col">
							不满足当前工作成就，寻找一切可以提升工作质量的机会和细节，制定行动计划，不断优化。
						</td>
					</tr>
					<tr>
						<td>行业融入感</td>
						<td class="col">
							持续关注互联网相关领域的产品、技术及行业信息。
						</td>
					</tr>
				</table>
			</div>
			<div class="line pa"></div>
			<div class="closeInstruction pa" @click="isWeightDescription = false"><img src="../../../static/images/errorClose.png" /></div>
		</div>
	</div>
	<!-- 权重说明结束 -->

	<!--加载框开始-->
	<div class="backgroundBlack" v-show="loadings">
		<div class="floatingCirclesG">
			<div class="f_circleG frotateG_01"></div>
			<div class="f_circleG frotateG_02"></div>
			<div class="f_circleG frotateG_03"></div>
			<div class="f_circleG frotateG_04"></div>
			<div class="f_circleG frotateG_05"></div>
			<div class="f_circleG frotateG_06"></div>
			<div class="f_circleG frotateG_07"></div>
			<div class="f_circleG frotateG_08"></div>
		</div>
	</div>
	<!--加载框结束-->

	<!-- 提示框开始 -->
	<tip-box :tip-box-status.sync="tipsBox.isShow" :tip-title="tipsBox.tipTitle" :tip-content="tipsBox.tipContent" :tip-button="tipsBox.tipButton"
		:tip-confirm-btn="tipsBox.tipConfirmBtn" :tip-cancel-btn="tipsBox.tipCancelBtn" :tip-box-close.sync="tipsBox.tipBoxClose">
	</tip-box>
	<!--提示框结束-->

	<div class="buttonSeries">
		<div class="buttonSeriesBox clearfix">
			<button class="btn" @click="abandonShow">放弃</button>
			<button class="btn btn1" @click="replaceShow">更换处理人</button>
			<button class="btn" @click="save">保存</button>
			<button class="btn" @click="submit">提交</button>
		</div>
	</div>
	<!--放弃-->
	<abandon :back-abandon.sync="backAbandonShow.showAbandon" @abandon="getAbandon" v-if="backAbandonShow.showAbandon"></abandon>
	<!--更换处理人-->
	<replace :back-replace.sync="backReplaceShow.showReplace" v-if="backReplaceShow.showReplace"  @replace="getReplace"></replace>

</template>
<script lang="babel">
	import interviewPublic from '../Parts/interviewPublic.vue'
	import rightHead from '../common/rightHead.vue'
	import tipBox from '../common/tipBox.vue'
	import approveAdvise from '../Parts/approveAdvise.vue'
	import abandon from '../Parts/abandon.vue'
	import replace from '../Parts/replace.vue'
	export default {
		components: {
			interviewPublic: interviewPublic,
			rightHead: rightHead,
			approveAdvise:approveAdvise,
			abandon:abandon,
			replace:replace,
			tipBox:tipBox			
		},
		data: function() {
			return {				
				//放弃
				backAbandonShow:{
					showAbandon:false
				},
				//更换处理人
				backReplaceShow:{
					showReplace:false
				},
                // 加载状态
                loadings: false,
                // 提示框
                tipsBox: {
                    "isShow": false,
                    "tipContent": "内容",
                    "tipBoxClose": true,
                    "tipButton": 1,
                    "tipConfirmBtn": function () {
                    },
                    "tipCancelBtn": function () {
                    }
                },
                total: 0,
                // 获取面试评价数据信息
                interviewComment: [],
                // 必须要填的值
                hintData: {
                	// 员工类型
                	"userType":'正式',
                    // 职位体系
                    "checkedNames":'',
                    // 职位类
                    "position": '',
                    // 安排职位
                    "arrange": '',
                    // 建议职级
                    "rank": '',
                    // 建议子等
                    "subset":'',
                    // 面试评价
                    "comment": '',
                    // 面试评分
                    "score": '',
                    // 面试评语
                    "comments": '',
                    // 风险不足
                    "risk": '',
                    // 笔试方向及成绩
                    "achievement": '',
                    // 综合评价
                    "evaluate": ''
                },
                // 获取面试评分表格内容
                auditionScore: [
                    {
                        "id": 1,
                        "project": "专业知识",
                        "weight": "25%",
                        "evaluationDetails": "",
                        "score": 0
                    },
                    {
                        "id": 2,
                        "project": "专业技能",
                        "weight": "25%",
                        "evaluationDetails": "",
                        "score": 0
                    },
                    {
                        "id": 3,
                        "project": "用户",
                        "weight": "15%",
                        "evaluationDetails": "",
                        "score": 0
                    },
                    {
                        "id": 4,
                        "project": "好学",
                        "weight": "20%",
                        "evaluationDetails": "",
                        "score": 0
                    },
                    {
                        "id": 5,
                        "project": "实干",
                        "weight": "15%",
                        "evaluationDetails": "",
                        "score": 0
                    }
                ],
                // 所有的分数之和
                arrScore: ["", "", "", "", ""],
                // 展示说明
                isWeightDescription: false,
				// 是否点击了更多操作
				isBtnClick: false
			}
		},
		methods: {
			// 获取面试评价信息
            getpositionClassList: function () {
                this.loadings=true;
                this.$http.get('static/data/jobDetails.json').then(function (res) {
                	this.loadings=false;
                    res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                    if (res.data.Result === true) {
                        //  获取面试评价
                        this.interviewComment = res.data.interviewComment;
                    } else {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "请检查网络！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    }
                }).catch(function (res) {
                	this.loadings=false;
                });
            },
            // 计算总分       
            mark: function (index,value) {
            	this.auditionScore[index].score = value;
                this.arrScore[index] = value;
                var num = 0;
                var scaleMarks = [0.25, 0.25, 0.15, 0.2, 0.15]
                for (var i = 0; i < this.arrScore.length; i++) {
                    num = this.arrScore[i] * scaleMarks[i] + num;
                }
                this.total  = num.toFixed(2);
            },
            //更换处理人
			replaceShow:function(){
				this.backReplaceShow.showReplace=true;
				this.getBtnClick();
			},
			//放弃是否显示
			abandonShow:function(){
				this.backAbandonShow.showAbandon=true;
			},
			// 获取更换处理日传值的数据
			getReplace: function(val) {
                this.loadings=true;
                let params = {
	                'userName': val.replacePerson,
	                'userId': val.replacePersonId,
	                'cause': val.replaceReason
            	};
                this.$http.post('api/SetReplace',{"params": params}).then(function (res) {
                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                	this.loadings=false;
                    if (res.data.Result === true) {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "操作成功！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    } else {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "请检查网络！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    }
                }).catch(function (res) {
                	this.loadings=false;
                });
			},
			// 获取放弃传值的数据
			getAbandon: function (val) {
                this.loadings=true;
                let params = {
	                'cause': val.abandonReason
            	};
                this.$http.post('/api/SetAbandon',{"params": params}).then(function (res) {
                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                	this.loadings=false;
                    if (res.data.Result === true) {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "操作成功！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    } else {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "请检查网络！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    }
                }).catch(function (res) {
                	this.loadings=false;
                });
			},
			// 保存
			save: function () {
                this.loadings=true;
                let params = {
	                'userType': this.hintData.userType,
	                'checkedNames': this.hintData.checkedNames,
	                'position': this.hintData.position,
	                'arrange': this.hintData.arrange,
	                'rank': this.hintData.rank,
	                'subset': this.hintData.subset,
	                'comment': this.hintData.comment,
	                'score': this.hintData.score,
	                'comments': this.hintData.comments,
	                'risk': this.hintData.risk,
	                'achievement': this.hintData.achievement,
	                'evaluate': this.hintData.evaluate
            	};
                this.$http.post('/api/SaveProfessinoal',{"params": params}).then(function (res) {
                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
                	this.loadings=false;
                    if (res.data.Result === true) {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "操作成功！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    } else {
						this.tipsBox.tipBoxClose = true;
	                    this.tipsBox.tipContent = "请检查网络！";
	                    this.tipsBox.tipButton = 2;
	                    this.tipsBox.isShow = true;
                    }
                }).catch(function (res) {
                	this.loadings=false;
                });
			},
			// 提交
			submit: function () {
                if (this.hintData.checkedNames && this.hintData.position && this.hintData.arrange && this.hintData.rank && this.hintData.subset && this.hintData.comment && this.hintData.risk && this.hintData.evaluate) {
                	this.loadings=true;
	                let params = {
		                'userType': hintData.userType,
		                'checkedNames': hintData.checkedNames,
		                'position': hintData.position,
		                'arrange': hintData.arrange,
		                'rank': hintData.rank,
		                'subset': hintData.subset,
		                'comment': hintData.comment,
		                'score': hintData.score,
		                'comments': hintData.comments,
		                'risk': hintData.risk,
		                'achievement': hintData.achievement,
		                'evaluate': hintData.evaluate
	            	};
	                this.$http.post('/api/SubmitProfessinoal',{"params": params}).then(function (res) {
	                	res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
	                	this.loadings=false;
	                    if (res.data.Result === true) {
							this.tipsBox.tipBoxClose = true;
		                    this.tipsBox.tipContent = "操作成功！";
		                    this.tipsBox.tipButton = 2;
		                    this.tipsBox.isShow = true;
	                    } else {
							this.tipsBox.tipBoxClose = true;
		                    this.tipsBox.tipContent = "请检查网络！";
		                    this.tipsBox.tipButton = 2;
		                    this.tipsBox.isShow = true;
	                    }
	                }).catch(function (res) {
	                	this.loadings=false;
	                });
                	window.location.href = "http://www.qq.com";
                } else {
					this.tipsBox.tipBoxClose = true;
                    this.tipsBox.tipContent = "请将必填项填写完整！";
                    this.tipsBox.tipButton = 2;
                    this.tipsBox.isShow = true;                	
                }
			},
			// 获取面试评语
			getComments: function (msg) {
				this.hintData.comments = msg;
			},
			// 获取风险不足
			getRisk: function (msg) {
				this.hintData.risk = msg;
			},
			// 获取笔试方向及成绩
			getAchievement: function (msg) {
				this.hintData.achievement = msg;
			},
			// 滚动至顶部
			getBtnClick: function (msg) {
				this.isBtnClick = true;
				windowGlobal.myScroll();
			}
		},
		ready: function() {
            // 获取职位类选择器列表
            this.getpositionClassList();
		}
	}
</script>


