<style lang="sass">
    /*提示框*/
    
    div,
    ul,
    ol,
    li,
    input {
        margin: 0;
        padding: 0;
        color: black;
        box-sizing: border-box;
    }
    
    .tipBox {
        /*width: 320px;*/
        min-height: 30px;
        border: 1px solid #ccc;
        border-radius: 10px;
        background-color: white;
        position: fixed;
        top: 50%;
        left: 50%;
        z-index: 99;
        margin-top: -60px;
        font-size: 14px;
    }
    
    .tipBox > div {
        width: 100%;
        min-height: 30px;
    }
    
    .tipTitle {
        line-height: 40px;
        height: 40px;
        border-bottom: 1px solid #3CA00A;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        color: #3CA00A;
        background-color: white;
        font-weight: bold;
    }
    
    .tipTitle > span {
        float: left;
        margin-left: 20px;
    }
    
    .tipTitle > label {
        width: 40px;
        height: 40px;
        border-left: 1px solid #3CA00A;
        display: block;
        float: right;
        text-align: center;
    }
    
    .tipContent {
        width: 100%;
        padding: 20px;
        line-height: 30px;
        text-align: center;
    }
    
    .tipBtn {
        width: 100%;
        height: 50px;
        border-top: 1px solid #E6E6E8;
        clear: both;
    }
    
    .tipBtn > button {
        width: 100px;
        height: 30px;
        line-height: 30px;
        float: left;
        color: #3CA00A;
        background-color: white;
        border: 1px solid #3CA00A;
        border-radius: 6px;
        font-weight: bold;
    }
    
    button.tipBtnRight {
        float: right;
    }
    
    .tipBtn > div {
        display: inline-block;
        width: 49%;
        float: left;
        padding: 15px 0;
        text-align: center;
    }
    
    .tipBtn > div.col1 {
        border-right: 1px solid #E6E6E8;
    }
</style>
<template>
    <div class="backgroundBlack" v-show="tipBoxStatus">
        <div class="tipBox" :style="widths">

            <!-- <div class="tipTitle">
                <span v-text="tipTitle"></span>
                <label v-show="tipBoxClose" @click="hideBox">X</label>
            </div> -->

            <div class="tipContent" :class="[tipButton===1 ? 'tipCenter':'']">{{{tipContent}}}</div>

            <div class="tipBtn" v-if="tipButton===2">
                <div class="col1">
                    <a href="javascript:void(0)" class="link" @click="cancelBtn">取消</a>
                </div>
                <div class="col2">
                    <a href="javascript:void(0)" class="link" @click="confirmBtn">确定</a>
                </div>
            </div>

            <div class="tipBtn" v-if="tipButton===3">
                <div class="col1">
                    <a href="javascript:void(0)" class="link" @click="cancelBtn">取消</a>
                </div>
                <div class="col2">
                    <a href="javascript:void(0)" class="link" @click="confirmBtn">删除</a>
                </div>
            </div>

            <div class="tipBtn" v-if="tipButton===4">
                <div class="col1">
                    <a href="javascript:void(0)" class="link" @click="cancelBtn">放弃</a>
                </div>
                <div class="col2">
                    <a href="javascript:void(0)" class="link" @click="confirmBtn">重试</a>
                </div>
            </div>
            <!-- <div class="tipBtn" v-else :class="[tipButton===1 ? 'tipCenter':'']">
                <button type="button" @click="confirmBtn" style="float: none;">确定</button>
            </div> -->

        </div>
    </div>
</template>
<script lang="babel">
    export default{
        name: "tipBox",
        props: {
            tipBoxWidth: {
                type: String,
                default: "280"
            },
            tipTitle: {
                type: String,
                default: "温馨提示"
            },
            tipContent: {
                type: String,
                default: "提示信息"
            },
            tipButton: {
                type: Number,
                default: 1
            },
            tipConfirmBtn: {
                type: Function,
                default: function () {
                }
            },
            tipCancelBtn: {
                type: Function,
                default: function () {
                }
            },
            tipBoxStatus: {
                type: Boolean,
                default: true,
                twoWay: true
            },
            tipBoxClose: {
                type: Boolean,
                default: true,
                twoWay: true
            }
        },
        computed: {
            widths: function () {
                return {width: this.tipBoxWidth + 'px', 'margin-left': '-' + (this.tipBoxWidth / 2) + "px"};
            }
        },
        data: function () {
            return {};
        },
        methods: {
            hideBox: function () {
                this.tipBoxStatus = false;
            },
            confirmBtn: function () {
                this.tipConfirmBtn();
                this.hideBox();
            },
            cancelBtn: function () {
                this.tipCancelBtn();
                this.hideBox();
            }
        }
    };
</script>


