<style scoped lang="sass">
	div.reject {
		position: fixed;
		z-index: 4;
		top: 0;
		width: 100%; 
	}
	
	.rejectCon {
		margin-top: 10px;
		.rejectPerson {
			.rejectTo {
				background-color: #fff;
				border-top: 1px solid rgba(204, 204, 204, 1);
			}
			h4 {
				border-bottom: 1px solid rgba(204, 204, 204, 1);
				padding-bottom: 5px;
				font-size: 14px;
				font-weight: normal;
				padding: 0px 0 0px 20px;
				i {
					float: right;
					margin-right: 20px;
					margin-top: 13px;
				}
				.info {
					padding-top:13px;
					float:left;
					margin-right:5px;
				}
			}
			.rejectReason {
				width: 100%;
				h4 {
					padding: 20px;
					border: none;
				}
				div {
					margin: 0 auto;
					width: 95%;
				}
			}
			input,
			textarea {
				outline: none;
				border: none;
				resize: none;
				width: 100%;
				border-radius: 5px;
			}
		}
	}
	
	.rejectBtn {
		margin-top: 40px; 
		width: 100%;
		.btn {
			width: 100%;
			height: 50px;
			font-size: 16px;
		}
		div {
			margin: 0 auto;
			width: 96%;
		}
		div:nth-of-type(1) {
			.btn {
				background: #30a6e4;
				color: #fff;
			}
		}
		div:nth-of-type(2) {
			margin-top: 20px;
			.btn {
				background: #efeff4;
				color: #30a6e4;
				border: 1px solid #30a6e4;
			}
		}
	}
	/*提交成功失败*/
	
	span.block {
		height: 95px;
		text-align: center;
		line-height: 95px;
	}
	
	.Btnfail {
		width: 50%;
		border-top: 1px solid #666;
		height: 40px;
		text-align: center;
		line-height: 40px;
	}
	
	.Btnfail:nth-of-type(1) {
		box-sizing: border-box;
		border-right: 1px solid #666;
	}
</style>
<template>
	<div v-show="backShowChildren" id='idd' class="reject"  :style="{'height':height+'px'}">
		<div class="rejectCon">
			<div class="rejectPerson">
				<div class="rejectTo" @click="rejectTo">
					<h4 style="padding-top:0; padding-bottom:0;">
						<img src="../../../static/images/star.png" width="10" class="info" />
						<label for="" class="info" >驳回至</label>
						<div style="display: inline-block;width: 60%;overflow: hidden;" class="pr">
							<select name="" style="width: 500px;margin-left: 20px; height:40px;" @click="setScroll()" v-model="submitData.rejectPerson">
								<option value="" selected>请选择</option>
								<option value="{{item.name}}" v-for="item in rejectItems">{{item.name}}</option>
								{{rejectItems}}
							</select>
						</div>
						<i><img :src="gt.gtImg" :alt="gt.gtText" width="10"/></i>
					</h4>
				</div>
				<div class="rejectReason">
					<h4>
						<img src="../../../static/images/star.png" width="10" /> 驳回原因
					</h4>
					<div>
						<textarea name="" rows="12" cols="" placeholder="请输入原因" v-model="submitData.rejectReason" @click="setScroll()" ></textarea>
					</div>
				</div>
			</div>
		</div>
		<div class="rejectBtn">
			<div><button class="btn" @click="submitBtn($event)">提交</button></div>
			<div><button class="btn" @click="backBtn">返回</button></div>
		</div>
		<!--检测浮层-->
		<div class="inputPoint" v-show="inputPointShow">
			<div class="inputPointMsg ">
				请将必填项填写完整
			</div>
		</div>
		<div v-show="showfloat">
			<div v-if="success" class="successBtn" :style="widths">
				<button class="btn btnSuccess">操作成功</button>
			</div>
			<div v-else class="failBtn">
				<div>
					<span class="block">操作失败，请检查网络后重试。</span>
				</div>
				<div class="clearfix">
					<span class="fl Btnfail" @click="showfloat=false">取消</span>
					<span class="fl Btnfail" @click="submitBtn">重试</span>
				</div>
			</div>
		</div>
	</div>
</template>
<script lang="babel">
	export default{
		name:'reject',
		data:function(){
			return{
				// 页面高度
				height: '',
				idd:'',
				success:true,
				marginLeft:'',
				showfloat:false,
				widths:'',
				gt:{
					gtImg:'static/images/gt.png',
					gtText:'大于号'
				},
				//信息填写不完全的提示
				inputPointShow:false,
				//驳回至列表
				rejectItems:'',
				//rejectData
				submitData:{
					rejectPerson:'',
					rejectReason:''
				}
			}
		},
		props:{
			backShowChildren:{
				type:Boolean,
				default:true,
				twoWay:true
			}
		},
		methods:{
			// 设置弹出层高度
			getWindowGlobal: function () {
				this.height = window.screen.height;
			},
			//提交按钮
			submitBtn:function (e) {
				if(this.submitData.rejectPerson==""||this.submitData.rejectReason==""){
					this.inputPointShow=true;
					var that=this;
					setTimeout(function(){
						that.inputPointShow=false;
					},2000)
				}else{
						this.$http.get('api/setHrdExmine',{'params': this.submitData}).then(function(res){
							res.data = $.parseJSON(res.data) == null? res.data: $.parseJSON(res.data);
							if(res.data.Result==true){
								this.showfloat=true;
								this.success=true;
								var that=this;
								//成功之后，返回当前页面
								setTimeout(function(){
									that.success=false;
									that.backShowChildren=false;
								},1000)
							}
						}).catch(function (res) {
								this.showfloat=true;
								this.success=false;
		                });
				}
		        this.backShowChildren = false;
				windowGlobal.enableScroll();
			},
			//返回按钮
			backBtn:function () {
				this.backShowChildren=false;
				windowGlobal.enableScroll();
			},
			//点击驳回至
			rejectTo:function () {
			},
			// 设置滚动
			setScroll: function () {
				windowGlobal.myScroll();
			}
		},
		ready:function () {
			windowGlobal.disableScroll();
			this.$http.get('static/data/reject.json').then((response) => {
				response.data = $.parseJSON(response.data) == null? response.data: $.parseJSON(response.data);
				this.rejectItems=response.data.data;
			}, (response) => {});
			this.getWindowGlobal();
		},
		events: {
            'hrd-msg': function(msg) {
                this.idd=msg;
            }
        }
        // ,
        // watch: {
        //     backShowChildren (value) {
        //         // 在显示时去掉body滚动条，防止出现双滚动条
        //         if (value) {                	
        //         	// $("body").bind("touchmove",function(event){event.preventDefault;});
        //             document.body.className += ' modal-open';
        //         }
        //         // 在modal动画结束后再加上body滚动条
        //         else {
        //         	// $("body").unbind("touchmove");
        //            document.body.className = document.body.className.replace(/\s?modal-open/, '');
        //         }
        //     }        
        // }
	}
</script>


